import React, { Component } from "react";
import { GenericSelector } from "../../components/CustomInput/GenericSelector";
import { AuthFetch } from "../../utils/AuthFetch";

export class PersonSelector extends Component {
  state = { options: [] };
  mapOptions(options) {
    return options.map(item => (
      <option key={item.id} value={item.id}>
        {item.fullname}
      </option>
    ));
  }
  async loadOptions(type) {
    const fetch = new AuthFetch("api/persons");
    const persons = await fetch.get({ type });
    return persons.data;
  }
  async componentDidMount() {
    let options = await this.loadOptions(this.props.type);
    this.setState({ options });
  }
  render = () => {
    const { name, title, value, ...rest } = this.props;
    const options = this.state.options;
    return (
      <GenericSelector
        title={title}
        value={value}
        {...rest}
        name={name || "selector"}
      >
        {this.mapOptions(options)}
      </GenericSelector>
    );
  };
}
