import React, { Component } from "react";
import GridItem from "components/Grid/GridItem.jsx";
import GridContainer from "components/Grid/GridContainer";
import { AnchorList } from "../../components/Anchors/AnchorList";
import { AuthFetch } from "../../utils/AuthFetch";
import { ListItem } from "@material-ui/core";
import List from "@material-ui/core/List";
import { IconList } from "../../components/Containers/IconList";

import "./scss/ReduceUserProfile.scss";
import { insteadIMG } from "../../utils/Utils";
import { Image } from "../../components/Images/Image";
import QR from "../../components/QR/Qr";

/* eslint eqeqeq: 0*/

export class ReduceUserCard extends Component {
  constructor(props) {
    super(props);
    this.state = { ...props };
  }
  componentDidMount() {
    this.remoteload(this.state.id);
  }
  async componentWillReceiveProps(nextProps) {
    let value = nextProps.id;
    if (value != this.state.id) {
      this.remoteload(value);
    }
  }
  async reloadQr(id) {
    const fetch = new AuthFetch(`api/persons/${id}`);
    let user = await fetch.put({ hash: true });
    this.setState({
      hash: user.data.hash
    });
  }
  async remoteload(id) {
    if (id == null) {
      return false;
    }
    const fetch = new AuthFetch(`api/persons/${id}`);
    const person = (await fetch.get()).data;
    this.setState(person);
  }
  render() {
    const { photo, fullname, phones, mails, id, hash } = this.state;
    return (
      <GridContainer className="reduceUserProfile">
        <GridItem xs={12} lg={4}>
          <Image
            src={insteadIMG(photo)}
            alt="..."
            style={{
              maxHeight: "300px",
              boxShadow: "1px 1px 1px",
              borderRadius: "9px"
            }}
          />
        </GridItem>
        <GridItem xs={12} lg={4}>
          <GridContainer>
            <GridItem xs={12}>
              <IconList icon="user" title="Nombre Completo">
                {fullname}
              </IconList>
            </GridItem>
            <GridItem xs={12}>
              <IconList icon="mail-bulk" title="Correos">
                <AnchorList type="mailto" items={mails} />
              </IconList>
            </GridItem>
            <GridItem xs={12}>
              <IconList icon="mail-bulk" title="Teléfonos">
                <AnchorList type="tel" items={phones} />
              </IconList>
            </GridItem>
          </GridContainer>
          <List>
            <ListItem />
          </List>
        </GridItem>
        <GridItem xs={12} lg={4}>
          <QR
            onReload={() => {
              this.reloadQr(id);
            }}
            data={JSON.stringify({ h: hash })}
          />
        </GridItem>
      </GridContainer>
    );
  }
}
