import React, { Component } from "react";
import { AuthFetch } from "../../utils/AuthFetch";
import { ActionInput } from "../../components/CustomInput/ActionInput";
import { FontIcon } from "../../components/Icons/FontIcon";
import { SimpleInput } from "../../components/CustomInput/SimpleInput";
import GridItem from "components/Grid/GridItem.jsx";
import { optionalFn } from "../../core/helpers";
export class ConceptsList extends Component {
  constructor(props) {
    super(props);
    this.state = {
      concepts: [],
      id: null
    };
  }
  async loadConcepts() {
    const fetch = new AuthFetch("api/finances/concepts");
    let concepts = (await fetch.get()).data;
    optionalFn(this.props.onChange)(concepts);
    this.setState({ concepts });
  }
  componentDidMount() {
    this.loadConcepts();
  }
  /**
   *
   * @param string concept
   * @param bool edit
   */
  async _addConcept(concept, edit = false) {
    let id = edit || "";
    const fetch = new AuthFetch(`api/finances/concepts/${id}`);
    if (edit) {
      await fetch.put({ concept });
    } else {
      await fetch.post({ concept });
    }
    this.loadConcepts();
  }
  render() {
    const { concepts } = this.state;
    return (
      <>
        <ActionInput
          name="concepto"
          title="Concepto"
          icon={<FontIcon iconName="fa-plus" />}
          onClick={value => {
            this._addConcept(value);
            return "";
          }}
          value=""
        />
        {concepts.map((item, key) => {
          return (
            <GridItem xs={12}>
              <SimpleInput
                key={key}
                name="concepto"
                title="Concepto"
                onBlur={ev => {
                  this._addConcept(ev.target.value, item.id);
                  return ev.target.value;
                }}
                value={item.concept}
              />
            </GridItem>
          );
        })}
      </>
    );
  }
}
