import React, { useState, useEffect, useReducer } from "react";
import { Helpers, optionalFn, playAudio } from "../../core/helpers";
import { SearchUsers } from "./../Users/SearchUsers";
import { CustomModal } from "../../components/CustomModal/CustomModal";
import { VisitorsForm } from "./VisitorsForm";

import { QrTicket } from "../../components/QR/QrTicket";
import validAccess from "assets/sound/validAccess.mp3";
import { hasAccess } from "./utils/AccessUtils";
import { ModalStreet } from "./ModalStreet";
import { ConditionalWall } from "../../components/FilterWall/ConditionalWall";
import swal from "sweetalert";
import { itsDebtor } from "../Finances/core/FinancesUtils";
import { Persons } from "../../utils/Controller/Persons";
import { accessDenied } from "assets/sound/accessDenied.mp3";
import { DoorSelector } from "./DoorSelector";
import { AccessPanel } from "./AccessPanel";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import GridItem from "components/Grid/GridItem.jsx";
/* eslint eqeqeq:0 */

export function LegoAccess() {
  const [search, setSearch] = useState();
  const [door, setDoor] = useState(null);
  const [process, setProcess] = useState(false);
  const [person, setPerson] = useState("{}");
  const [openVisitors, toggleVisitors] = useState(false);
  /**
   * Search permite buscar en la api.
   * @param String search
   * @return Promise
   */
  const willSearch = value => {
    const helpers = new Helpers();
    value = value.toLowerCase();
    const qrCode = helpers.isJsonString(value);
    if (qrCode === false) {
      return true;
    }
    loadPersonByHash(qrCode.h);
    return "";
  };
  const loadPersonByHash = async hash => {
    const person = await new Persons().by({ hash });
    if (person.length === 0) {
      playAudio(accessDenied);
      swal({
        title: "Advertencia",
        text: `QR invalido`,
        timer: 3000,
        icon: "warning"
      });
      setSearch("");
      return false;
    }
    setPerson(JSON.stringify(person[0]));
    setProcess(true);
  };
  if (door == null) {
    return (
      <PanelContainer title="Seleccione Puerta" subtitle=" ">
        <GridItem xs={12}>
          <DoorSelector
            title="Seleccione la puerta"
            value={door}
            onChange={(door, data) => {
              setDoor(data[0]);
              console.log(data);
            }}
          />
        </GridItem>
      </PanelContainer>
    );
  }
  return (
    <>
      <PanelContainer
        title={door ? door.alias : "Puerta de acceso"}
        subtitle={"Seleccione puerta de acceso"}
      >
        <ConditionalWall conditional={process == false}>
          <SearchUsers
            key={search}
            value={search}
            willSearch={willSearch}
            onInteraction={(ev, person) => {
              setPerson(JSON.stringify(person));
              setProcess(true);
            }}
            onAddClick={() => {
              toggleVisitors(true);
              setProcess(true);
            }}
          />
        </ConditionalWall>
        <AddVisitor
          door={door}
          open={openVisitors}
          key={"vs" + openVisitors}
          onClose={() => {
            setPerson("{}");
            setProcess(false);
          }}
          onSubmission={person => {
            setPerson(JSON.stringify(person));
            toggleVisitors(false);
          }}
        />
        <AccessLoader
          door={door}
          person={JSON.parse(person)}
          open={person != "{}"}
          key={JSON.parse(person).id}
          onClose={() => {
            setProcess(false);
          }}
          onEnd={() => {
            setPerson("{}");
            setProcess(false);
          }}
        />
      </PanelContainer>
    </>
  );
}
/**
 * Abre el formulario de visitas y procede con el flujo de acceso
 * @param {open,*} param0
 */
function AddVisitor({ open, onSubmission, ...rest }) {
  const [person, setPerson] = useState("{}");
  const prs = JSON.parse(person);
  return (
    <>
      <AccessTicket
        person={prs}
        address={prs.address}
        open={person !== "{}"}
        onPrint={() => {
          console.log(person);
          optionalFn(onSubmission)(prs);
        }}
      />

      <CustomModal open={open} {...rest}>
        <VisitorsForm
          onSubmission={ev => {
            setPerson(JSON.stringify(ev));
          }}
        />
      </CustomModal>
    </>
  );
}
/**
 * Determina el orden de eventos.
 * @param {door,person,open} param0
 */
function AccessLoader({ door, person, open = false, onEnd, ...rest }) {
  const [access, toggleAccess] = useState(false);
  const peatonalConfirm = async () => {
    if (!open || door.type != 2) {
      toggleAccess(door.type);
      return false;
    }

    let response = await swal({
      title: "Acceso Peatonal",
      text: `¿${person.fullname} esta entrando o saliendo?`,
      icon: "warning",
      showCancelButton: false,
      type: "input",
      buttons: { cancel: "Salir", confirm: "Entrar" }
    });
    toggleAccess(response ? 1 : 0);
  };

  const checkDebts = () => {
    itsDebtor(person.id).then(ev => {
      peatonalConfirm();
    });
  };
  useEffect(() => {
    if (open) checkDebts();
  }, []);

  if (!open) return false;

  return (
    <>
      <ConditionalWall conditional={access === 1}>
        <EntryFlux person={person} door={door} onEnd={onEnd} {...rest} />
      </ConditionalWall>
      <ConditionalWall conditional={access === 0}>
        <ExitFlux person={person} door={door} onEnd={onEnd} {...rest} />
      </ConditionalWall>
    </>
  );
}
/**
 * Inicia flujo de de cambio de domicilio y ticket
 * @param {person,skip:deprecated,onChange} param0
 */
function PersonValidator({ person, skip = false, onChange, onClose }) {
  const canAccess = hasAccess(person);
  const [state, setState] = useReducer(
    (state, newState) => ({ ...state, ...newState }),
    { modal: !canAccess && !skip, ticket: false, address: "" }
  );
  const { modal, ticket, address } = state;
  useEffect(() => {
    if (canAccess) {
      optionalFn(onChange)();
    }
  }, []);
  return (
    <>
      <ModalStreet
        personId={person.id}
        open={modal}
        onChange={(ev, street) => {
          setState({ modal: false, ticket: true, address: street });
        }}
        onClose={onClose}
      />
      <AccessTicket
        person={person}
        address={address}
        open={ticket}
        onPrint={async () => {
          console.log(state);
          optionalFn(onChange)();
        }}
      />
    </>
  );
}
/**
 * Abre el ticket de acceso.
 * @param {person,address,open,onPrint} param0
 */
function AccessTicket({ person, address, open, onPrint }) {
  return (
    <ConditionalWall conditional={open}>
      <QrTicket
        hash={JSON.stringify({ h: person.hash })}
        name={person.fullname}
        details={[
          person.plate,
          address,
          "Conserve su ticket, sera requerido al salir de las instalaciones."
        ]}
        onPrint={onPrint}
        date={new Date().toLocaleDateString("es-MX", {
          year: "numeric",
          month: "long",
          day: "numeric",
          hour: "2-digit",
          minute: "2-digit"
        })}
      />
    </ConditionalWall>
  );
}
/**
 * Maneja el flujo de entradas
 * @param {person,onEnd} param0
 */
function EntryFlux({ person, onEnd, door, ...rest }) {
  const [panel, togglePanel] = useState(false);
  console.log(person);
  return (
    <>
      <ConditionalWall conditional={!panel}>
        <PersonValidator
          person={person}
          onChange={() => {
            console.log(panel);
            togglePanel(true);
          }}
          {...rest}
        />
      </ConditionalWall>
      <ConditionalWall conditional={panel}>
        <AccessPanel
          open={panel}
          door={door}
          doorType={1}
          {...person}
          onClose={() => {
            //togglePanel(false);
            optionalFn(onEnd)();
          }}
          onSave={() => {
            console.log("saved");
            playAudio(validAccess);
          }}
        />
      </ConditionalWall>
    </>
  );
}
/**
 * Controla el flujo de salida
 * @param {person} param0
 */
function ExitFlux({ person, door, onEnd }) {
  const [modal, togglePanel] = useState(true);
  return (
    <>
      <AccessPanel
        open={modal}
        {...person}
        door={door}
        doorType={0}
        onClose={() => {
          //togglePanel(false);
          optionalFn(onEnd)();
        }}
        onSave={() => {
          playAudio(validAccess);
          optionalFn(onEnd)();
        }}
      />
    </>
  );
}
