import React, { Component } from "react";
import { DataTable } from "./../../components/Table/DataTable";
import { AuthFetch } from "../../utils/AuthFetch";
import { localeDate, numberToMoney } from "./../../core/helpers";
import { UnrecognizedPaymentForm } from "./ UnrecognizedPayments";
export class UnrecognizedPaymentsTable extends Component {
  state = { content: [], isOpen: false, total: 0 };
  async loadPayments() {
    let total = 0;
    const fetch = new AuthFetch("api/payments/unrecognized");
    let content = (await fetch.get()).data;
    content = content.map(item => {
      total += parseFloat(item.amount) - parseFloat(item.balance);
      return [
        item.bank,
        item.bankCode,
        item.amount,
        item.balance,
        localeDate(item.registerDate)
      ];
    });
    this.setState({ content, isOpen: false, total });
  }
  componentDidMount() {
    this.loadPayments();
  }
  render() {
    const { content, isOpen, total } = this.state;
    return (
      <>
        <DataTable
          inModal={
            <UnrecognizedPaymentForm
              onSubmission={() => {
                this.loadPayments();
              }}
            />
          }
          modalOpen={isOpen}
          onClose={() => {
            this.loadPayments();
          }}
          title="Pagos no reconocidos"
          subtitle={`Hay ${numberToMoney(
            total
          )} en concepto de pagos no reconocido sin utilizar`}
          titles={["Banco", "Identificador", "Monto", "Balance", "Fecha"]}
          content={content}
        ></DataTable>
      </>
    );
  }
}
