import React, { useState, useEffect } from "react";
import { AuthFetch } from "../../utils/AuthFetch";
import { CardList } from "../../components/Lists/CardList";
import { FaButton } from "../../components/CustomButtons/FaButton";
import { numberToMoney, optionalFn } from "../../core/helpers";
import { ActionInput } from "../../components/CustomInput/ActionInput";
import { FontIcon } from "../../components/Icons/FontIcon";
import "./scss/houses.scss";
import { calcTotals, pay } from "../Finances/core/FinancesUtils";
import { NavLink } from "react-router-dom";
import Button from "components/CustomButtons/Button.jsx";
import { ConditionalWall } from "../../components/FilterWall/ConditionalWall";
import { CsvButton } from "../../components/Table/CsvButton";
import { Translator } from "../../utils/Translator";
import { PrintFromURL } from "../../components/Prints/PrintFromURL";
import { ModalService } from "../Finances/ServicesComponents";

/**
 *
 * @param {id,onDataLoad,onPaymentChange} param0
 */
export function HousesByPerson({ id, onDataLoad, onPaymentChange }) {
  const [houses, setHouses] = useState([]);
  const loadHouses = async () => {
    const fetch = new AuthFetch(`api/charge/${id}/persons`);
    let content = await fetch.get();
    setHouses(content.data);
    optionalFn(onDataLoad)(content.data);
  };

  useEffect(() => {
    loadHouses();
    // eslint-disable-next-line
  }, []);
  return (
    <div className="housesGrid">
      <CardList
        items={houses}
        titleKey={"code"}
        itemsByLine={3}
        modifier={(item, key) => {
          return (
            <HouseCard
              item={item}
              onClick={value => {
                pay(item.id, value).then(() => loadHouses());
                optionalFn(onPaymentChange)(value);
                return "";
              }}
            />
          );
        }}
      ></CardList>
    </div>
  );
}
function HouseCard({ item, onClick }) {
  const [payment, setPayment] = useState("0.0");
  const total = calcTotals(item.charges).total;
  return (
    <>
      <ul>
        <li>
          <ConditionalWall
            conditional={parseFloat(total).toFixed(3) > 0}
            or={<h3>Saldo: {numberToMoney(total)}</h3>}
          >
            <Button
              color="primary"
              onClick={() => {
                setPayment(total);
              }}
            >
              Deuda: {numberToMoney(total)}
            </Button>
          </ConditionalWall>
        </li>
        <li>
          <p>{item.areaM2} metros cuadrados</p>
        </li>
        <li>
          <NavLink to={`/residentes/${item.zone}-${item.houseNumber}`}>
            <FaButton icon="home" title={"Detalles"} />
          </NavLink>
          <NavLink to={`/cuenta/${item.id}`}>
            <FaButton title="Cargos" icon="dollar-sign" />
          </NavLink>
          <PrintFromURL
            title="Imprimir nota de remision"
            url={`/templates/charges/${item.id}`}
          />
          <CsvButton
            fileName={`Lista de Cargos de ${item.zone}-${item.houseNumber}`}
            content={item.charges.map(charge => [
              charge.id,
              charge.paymentDate,
              charge.import,
              new Translator(charge.type).get()
            ])}
            titles={["ID", "Fecha", "Tarifa", "Tipo"]}
          />
          <ModalService houseID={item.id} />
        </li>
        <li>
          <ActionInput
            type={"number"}
            onClick={val => {
              setPayment("0.0");
              onClick(val);
            }}
            title="Pagar"
            value={payment}
            icon={<FontIcon iconName={"fa-save"} />}
          ></ActionInput>
        </li>
      </ul>
    </>
  );
}
