import React, { useState } from "react";
import { AsyncDataTable } from "../../components/Table/AsycnDataTable";
import { RangeDate } from "../../components/CustomInput/RangeDate";
import { validInputDate } from "../../core/helpers";
import { Image } from "../../components/Images/Image";

export function AsyncHistory() {
  let tomorrow = new Date();
  tomorrow.setDate(tomorrow.getDate() + 1);
  const [since, setSince] = useState(new Date());
  const [until, setUntil] = useState(tomorrow);
  return (
    <AsyncDataTable
      key={since}
      path="api/access"
      title="Historial de Accesos."
      subtitle="Datos Generales Accesos."
      hideButton
      args={{ since, until }}
      prev={
        <RangeDate
          date1={validInputDate(since)}
          date2={validInputDate(until)}
          onChange={values => {
            setSince(values.since);
            setUntil(values.to);
          }}
        />
      }
      titles={{
        photo: "foto",
        fullname: "Nombre",
        inside: "Estatus",
        plate: "Placa",
        door: "Puerta",
        houseCode: "Destino",
        created_at: "Fecha"
      }}
      formatContent={item => {
        return [
          <Image
            src={item.photo}
            style={{
              width: "50px",
              height: "50px",
              borderRadius: "100%",
              boxShadow: "1px 1px 3px #007777"
            }}
          />,
          item.fullname,
          item.inside ? "Entrada" : "Salida",
          item.plate,
          item.door,
          item.houseCode,
          new Date(item.created_at.date).toLocaleDateString("es-MX", {
            year: "numeric",
            month: "numeric",
            day: "numeric",
            hour: "2-digit",
            minute: "2-digit"
          })
        ];
      }}
    />
  );
}
