import React, { Component } from "react";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import GridItem from "components/Grid/GridItem.jsx";
import { ActionInput } from "../../components/CustomInput/ActionInput";
import { FontIcon } from "../../components/Icons/FontIcon";
import { CustomTable } from "../../components/Table/CustomTable";
import { AuthFetch } from "../../utils/AuthFetch";
import { ZoneList } from "./ZoneList";
import { CustomModal } from "components/CustomModal/CustomModal";
import { HouseRegister } from "./HouseRegister";
import { NavLink } from "react-router-dom";
import { FaButton } from "../../components/CustomButtons/FaButton";
import { PersonQRButton } from "../Users/PersonQR";
import { swal } from "sweetalert";

export class HousesView extends Component {
  constructor(props) {
    super(props);
    this.state = {
      data: this.localLoad() || [],
      zone: null,
      openRegister: false,
      search: "",
      filterByZone: null,
      houseID: ""
    };
  }
  componentDidMount() {
    this.loadHouses();
  }
  localLoad() {
    const fetch = new AuthFetch("api/houses");
    let houses = fetch.local();
    return this.formatHouses(houses);
  }
  async loadHouses(zone) {
    const fetch = new AuthFetch("api/houses");
    const data = zone ? { zone } : {};
    let houses = await fetch.get(data);
    houses = this.formatHouses(houses);
    this.setState({ data: houses, openRegister: false });
  }
  /**
   * Formatea la estructura del arreglo a imprimir.
   * @param [] houses
   * @return []
   */
  formatHouses(houses = []) {
    if (houses == null) {
      return [];
    }
    houses = houses.data;
    return houses.map((item, key) => {
      return [
        item.zone,
        item.houseNumber,
        `${item.areaM2} en m2`,
        item.owner.fullname,
        <>
          <PersonQRButton personID={item.owner.id} hash={item.owner.hash} />
        </>,
        item.resident.fullname,
        <>
          <PersonQRButton
            personID={item.resident.id}
            hash={item.resident.hash}
          />
        </>,
        <>
          <NavLink
            key={`house-${key}`}
            to={`/residentes/${item.zone}-${item.houseNumber}`}
          >
            <FaButton size="15px" icon="home" />
          </NavLink>
          <FaButton
            size="15px"
            icon="edit"
            onClick={() => {
              this.setState({
                openRegister: true,
                houseID: item.id
              });
            }}
          />
          <NavLink to={`/cuenta/${item.id}`} key={key}>
            <FaButton size="15px" icon="dollar-sign" />
          </NavLink>

          <FaButton
            size="15px"
            icon="door-open"
            style={{ color: "red" }}
            title="Vaciar propiedad"
            onClick={() => {
              const fetch = new AuthFetch(`api/houses/${item.id}`);
              fetch.delete().then(() => {
                swal("success", "Casa ha sido deshabitada", "success");
              });
            }}
          />
        </>
      ];
    });
  }

  render = () => {
    const { data, openRegister, search, filterByZone, houseID } = this.state;
    return (
      <PanelContainer
        title="Terrenos"
        subtitle="Lista de todos los terrenos"
        onAdd={() => {
          this.setState({ openRegister: true });
        }}
      >
        <GridItem xs={12} sm={4}>
          <ZoneList
            onChange={content => {
              this.loadHouses(content.value);
            }}
          />
        </GridItem>
        <GridItem xs={12} sm={8}>
          <ActionInput
            title="Buscar"
            value=""
            icon={<FontIcon iconName="fa-search" />}
            onClick={search => {
              this.setState({ search });
              return search;
            }}
          />
        </GridItem>
        <GridItem xs={12}>
          <CustomTable
            search={search}
            titles={[
              "Coto",
              "Lote",
              "Area",
              "Propietario",
              "Qr de prop",
              "Residente",
              "Qr de res",
              "Acciones"
            ]}
            data={data}
          />
        </GridItem>
        <CustomModal
          open={openRegister}
          onClose={() => {
            this.setState({ openRegister: false });
          }}
          styles={{ width: "30vw" }}
        >
          <HouseRegister
            id={houseID}
            onSave={() => {
              this.loadHouses(filterByZone);
            }}
          />
        </CustomModal>
      </PanelContainer>
    );
  };
}
