export class FinancesCore {
  finances = [];
  constructor(finances) {
    this.finances = finances;
  }
  /**
   * Permite obtener ingresos y egresos financieros.
   */
  getAccount() {
    const finances = this.finances;
    const account = { total: 0, income: 0, expenses: 0 };
    finances.map(item => {
      const amount = item.amount;
      account.total += amount;
      if (amount < 0) {
        account.expenses += amount;
      } else {
        account.income += amount;
      }
      return null;
    });
    return account;
  }
}
export function getPaymentMethodByKey(id) {
  const methods = {
    "1": "Efectivo",
    "2": "Cheque",
    "3": "Transferencia Electrónico de fondos",
    "4": "Tarjeta de crédito",
    "5": "Monedero electrónico",
    "6": "Dinero electrónico",
    "8": "Vales de despensa",
    "28": "Tarjeta de débito",
    "29": "Tarjeta de servicio",
    "99": "Otros"
  };
  return methods[id];
}
