import React, { useState } from "react";
import { FaButton } from "../../components/CustomButtons/FaButton";
import { optionalFn } from "../../core/helpers";
import { UsersContainer } from "../Forms/UsersContainer";
import { CustomModal } from "./../../components/CustomModal/CustomModal";
export function UserKeysButton({ id, onSave }) {
  const [open, toggle] = useState();
  return (
    <>
      <FaButton
        title="Definir Datos de usuario"
        icon="key"
        onClick={() => {
          toggle(true);
        }}
      />
      <CustomModal
        open={open}
        onClose={() => {
          toggle(false);
        }}
      >
        <UsersContainer
          submission={data => {
            toggle(false);
            optionalFn(onSave)(data);
          }}
          personID={id}
        />
      </CustomModal>
    </>
  );
}
