import React from "react";
import { GenericSelector } from "../../components/CustomInput/GenericSelector";
import { useState } from "react";
import { useEffect } from "react";
import { AuthFetch } from "../../utils/AuthFetch";
import { CustomModal } from "components/CustomModal/CustomModal";
import { optionalFn } from "../../core/helpers";
import { ConceptsList } from "./ConceptsList";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import { FaButton } from "../../components/CustomButtons/FaButton";
export function ConceptsSelector({ name = "concept", title, value, onChange }) {
  const [concepts, setConcepts] = useState([]);
  const [openModal, setModal] = useState(false);
  const loadConcepts = async () => {
    const fetch = new AuthFetch("api/finances/concepts");
    let list = (await fetch.get()).data;
    setConcepts(list);
  };
  const _handleChange = ev => {
    if (ev.value === "new") {
      setModal(true);
    }
    optionalFn(onChange)(ev);
  };
  useEffect(() => {
    loadConcepts();
  }, []);
  return (
    <>
      <GenericSelector
        title={title}
        name={name}
        onChange={_handleChange}
        value={value}
      >
        <option value="new">Agregar nuevo concepto</option>
        {concepts.map((item, key) => (
          <option value={item.id} key={key}>
            {item.concept}
          </option>
        ))}
      </GenericSelector>
      <CustomModal
        open={openModal}
        onClose={() => {
          setModal(false);
        }}
      >
        <PanelContainer
          title="Lista de conceptos"
          subtitle="Permite crear o editar conceptos"
        >
          <ConceptsList />
          <FaButton
            icon="save"
            onClick={() => {
              loadConcepts();
              setModal(false);
            }}
          />
        </PanelContainer>
      </CustomModal>
    </>
  );
}
