import React, { useState, useEffect } from "react";
import { GenericSelector } from "../../components/CustomInput/GenericSelector";
import { AuthFetch } from "../../utils/AuthFetch";
import { optionalFn } from "../../core/helpers";
export function HouseSelector({ onChange }) {
  const [houses, setHouses] = useState([]);
  const [value, setValue] = useState("");
  const loadHouses = async () => {
    const fetch = new AuthFetch("api/houses/");
    let data = (await fetch.get()).data;
    setHouses(data);
  };
  useEffect(() => {
    loadHouses();
  }, []);
  return (
    <GenericSelector
      onChange={ev => {
        const key = ev.value;
        optionalFn(onChange)(houses[key]);
        setValue(ev.value);
      }}
      value={value}
    >
      {houses.map((item, key) => {
        return <option value={key}>{item.code}</option>;
      })}
    </GenericSelector>
  );
}
