import React, { Component } from "react";
import { AjaxForm } from "../../components/Containers/AjaxForm";
import { SimpleInput } from "../../components/CustomInput/SimpleInput";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import { ZoneList } from "./ZoneList";
import { PersonSelector } from "../Users/PersonSelector";
import Button from "components/CustomButtons/Button.jsx";
import { Translator } from "../../utils/Translator";
import { GenericSelector } from "../../components/CustomInput/GenericSelector";
import GridItem from "components/Grid/GridItem.jsx";
export class HouseRegister extends Component {
  state = {
    houseNumber: "",
    zone: "",
    areaM2: "",
    ownerID: "",
    residentID: "",
    landUse: ""
  };
  loadHouseData = data => {
    this.setState(data.data);
  };
  render = () => {
    const id = this.props.id || "";
    const method = this.props.id ? "put" : "post";
    const {
      houseNumber,
      zone,
      areaM2,
      ownerID,
      residentID,
      landUse
    } = this.state;
    return (
      <AjaxForm
        autoload={id !== ""}
        path={`/api/houses/${id}`}
        method={method}
        willSubmit={inputs => {
          const { zone, houseNumber } = inputs;
          inputs.code = `${zone}-${houseNumber}`;
          if (typeof inputs.residentID === "undefined") {
            inputs.residentID = inputs.ownerID;
          }
          return inputs;
        }}
        submitted={data => {
          if (typeof this.props.onSave === "function") this.props.onSave(data);
        }}
        getter={this.loadHouseData}
      >
        <PanelContainer title="Registro" subtitle="de vivienda">
          <GridItem xs={12}>
            <ZoneList value={zone} />
          </GridItem>
          <GridItem xs={12}>
            <SimpleInput
              title="Numero de vivienda"
              name="houseNumber"
              value={houseNumber}
            />
          </GridItem>
          <GridItem xs={12}>
            <SimpleInput
              title="Area en metros cuadrados"
              name="areaM2"
              value={areaM2}
            />
          </GridItem>
          <GridItem xs={12}>
            <PersonSelector
              title="Propietario"
              name="ownerID"
              type="propietario"
              value={ownerID}
            />
          </GridItem>

          <GridItem xs={12}>
            <PersonSelector
              placeholder="Deja vacio en caso de que el Residente sea igual al Propietario"
              title="Residente"
              type="residente"
              name="residentID"
              value={residentID}
            />
          </GridItem>
          <GridItem xs={12}>
            <GenericSelector
              title="Uso de suelo"
              value={landUse}
              name="landUse"
            >
              <option value="Vivienda">Vivienda</option>
              <option value="Inhabitada">Inhabitada</option>
              <option value="Inhabitada">Inhabitada</option>
              <option value="Terreno">Terreno</option>
              <option value="Obra en proceso">Obra en proceso</option>
              <option value="Obra en suspendida">Obra en suspendida</option>
              <option value="Terraza">Obra</option>
            </GenericSelector>
          </GridItem>

          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={"submit"}
          >
            {new Translator("Save").get()}
          </Button>
        </PanelContainer>
      </AjaxForm>
    );
  };
}
