import React from "react";
import { AjaxForm } from "../../components/Containers/AjaxForm";
import GridItem from "components/Grid/GridItem.jsx";
import GridContainer from "components/Grid/GridContainer.jsx";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import { Button } from "@material-ui/core";
import { SimpleInput } from "./../../components/CustomInput/SimpleInput";
import { ConceptsSelector } from "./ConceptSelector";
import { Translator } from "../../utils/Translator";
import { GenericSelector } from "../../components/CustomInput/GenericSelector";
/* eslint eqeqeq: 0*/
export class FinancesForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      id: props.id || "",
      rfc: "XAXX010101000",
      description: "",
      type: "1",
      amount: "0.00",
      concept: "",
      paymentMethod: "99"
    };
  }
  _willSubmit(inputs) {
    inputs.amount *= inputs.type;
    return inputs;
  }
  _afterSubmit(inputs, jsx) {
    const submission = this.props.submission;
    if (typeof submission === "function") {
      return submission(inputs.data, jsx);
    }
  }
  _fillForm = inputs => {
    this.setState(inputs.data);
  };

  render() {
    const {
      rfc,
      concept,
      description,
      type,
      amount,
      paymentMethod
    } = this.state;
    const id = this.props.edit || "";
    const method = this.props.edit ? "put" : "post";
    return (
      <AjaxForm
        autoload={id != ""}
        path={`/api/finances/${id}`}
        method={method}
        willSubmit={this._willSubmit.bind(this)}
        submitted={this._afterSubmit.bind(this)}
        getter={this._fillForm}
      >
        <GridContainer>
          <GridItem xs={12} sm={12} md={12}>
            <PanelContainer title="Finanzas" subtitle="Fill the Form">
              <GridItem xs={12} sm={12} md={12}>
                <SimpleInput title="R.F.C" name="rfc" value={rfc} />
              </GridItem>

              <GridItem xs={12} sm={12} md={12}>
                <SimpleInput
                  title="Descripción"
                  name="description"
                  value={description}
                />
              </GridItem>
              <GridItem xs={12} sm={12} md={12}>
                <ConceptsSelector
                  title="Concepto"
                  name="conceptID"
                  onChange={ev => {
                    this.setState({ concept: ev.value });
                    return ev.value;
                  }}
                  value={concept}
                />
              </GridItem>
              <GridItem xs={12} sm={12} md={12}>
                <GenericSelector title="Tipo" name="type" value={type}>
                  <option value="1">Entrada</option>
                  <option value="-1">Salida</option>
                </GenericSelector>
              </GridItem>

              <GridItem xs={12}>
                <GenericSelector
                  name="paymentMethod"
                  title="Método de pago"
                  value={paymentMethod}
                >
                  <option value="99">Otros</option>
                  <option value="1">Efectivo</option>
                  <option value="2">Cheque</option>
                  <option value="3">Transferencia electronica de fondos</option>
                  <option value="4">Tarjeta de credito</option>
                  <option value="5">Monedero electronico</option>
                  <option value="6">Dinero electronico</option>
                  <option value="8">Vales de despensa</option>
                  <option value="28">Tarjeta de debito</option>
                  <option value="29">Tarjeta de servicio</option>
                </GenericSelector>
              </GridItem>
              <GridItem xs={12} sm={12} md={12}>
                <SimpleInput
                  title="Precio"
                  type="number"
                  step="0.5"
                  name="amount"
                  value={amount}
                />
              </GridItem>
              <Button
                type="submit"
                fullWidth
                variant="contained"
                color="primary"
                className={"submit"}
              >
                {new Translator("Save").get()}
              </Button>
            </PanelContainer>
          </GridItem>
        </GridContainer>
      </AjaxForm>
    );
  }
}
