import React, { Component } from "react";
import { DataTable } from "./../../components/Table/DataTable";
import { AuthFetch } from "./../../utils/AuthFetch";
import { AutoList } from "./../../components/Lists/AutoList";
export class LogsContainer extends Component {
    state = {
        logs: this.localLoad() || []
    };
    /**
     * @description carga la informacion del log de la copia del cache
     * @return {}
     */
    localLoad() {
        let fetch = new AuthFetch("api/logs");
        let logs = fetch.local();
        if (!logs) {
            return [];
        }
        logs = logs.data;
        logs = this._formatLog(logs);
        return logs;
    }
    /**
     * @description carga de manera asincrona la informacion de la tabla.
     */
    async loadLog() {
        let fetch = new AuthFetch("api/logs");
        let logs = (await fetch.get()).data;
        logs = this._formatLog(logs);
        this.setState({ logs });
    }
    componentDidMount() {
        this.loadLog();
    }
    _formatLog(logs = []) {
        return logs.map(item => {
            return [
                item.fullname,
                item.type,
                item.originIP,
                item.httpReffer,
                <AutoList list={JSON.parse(item.content)} />,
                item.path,
                item.method,
                new Date(item.created_at.date).toLocaleDateString()
            ];
        });
    }
    render() {
        const { logs } = this.state;
        return (
            <React.Fragment>
                <DataTable
                    hideButton
                    title="Bitacora del manantial"
                    subtitle="Registro de actividad"
                    titles={[
                        "Usuario",
                        "Tipo",
                        "Desde",
                        "Vista",
                        "Contenido",
                        "Destino",
                        "Metodo",
                        "Fecha"
                    ]}
                    content={logs}
                />
            </React.Fragment>
        );
    }
}
