import React, { useEffect } from "react";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import GridItem from "components/Grid/GridItem.jsx";
import { SimpleInput } from "../../components/CustomInput/SimpleInput";
import { useState } from "react";
import { optionalFn } from "../../core/helpers";
import { CameraView } from "../../components/UploaderInputs/CameraView";
import { Button } from "@material-ui/core";
import { AuthFetch } from "../../utils/AuthFetch";
import { StreetSelector } from "../Houses/StreetSelector";
import { ConditionalModal } from "../../components/CustomModal/ConditionalModal";
import GridContainer from "components/Grid/GridContainer.jsx";
import { FontIcon } from "../../components/Icons/FontIcon";
import "./scss/CallModal.scss";
/*eslint eqeqeq: 0*/
export function VisitorsForm({ onSubmission }) {
  const [modal, toggleModal] = useState(false);
  const [form, setForm] = useState({
    fullname: "",
    address: "",
    plate: "",
    description: ""
  });
  const [myPhoto, setMyPhoto] = useState(null);
  const _willSubmit = async inputs => {
    if (myPhoto) {
      inputs.photo = myPhoto;
    }
    inputs.type = "Visitantes";
    inputs.limitedAccess = new Date().toISOString().substring(0, 10);
    __submit(inputs);

    return inputs;
  };

  const _afterSubmit = inputs => {
    inputs.address = form.address;
    optionalFn(onSubmission)(inputs);
  };
  const __submit = async inputs => {
    _storeCars(inputs);
    const fetch = new AuthFetch("/api/persons/");
    let response = (await fetch.post(inputs)).data;
    _setVisitor({ houseCode: form.address, personID: response.id });
    _afterSubmit(response);
  };
  const _storeCars = inputs => {
    const fetch = new AuthFetch("api/cars");
    return fetch.post(inputs);
  };
  const _setVisitor = ({ houseCode, personID }) => {
    const fetch = new AuthFetch("api/residents/");
    fetch.post({ houseCode, personID });
  };
  return (
    <PanelContainer
      title="Registro de visitantes"
      subtitle="Llama por teléfono antes de registrar al usuario."
    >
      <GridItem xs={12} sm={12} md={12}>
        <CameraView
          autoStart={true}
          onPhotoTaken={image => {
            setMyPhoto(image);
          }}
          photo={myPhoto}
        />
      </GridItem>
      <GridItem xs={12}>
        <SimpleInput
          upperCase
          title="Nombre completo"
          name="fullname"
          value={form.fullname}
          onBlur={ev => {
            setForm({ ...form, fullname: ev.target.value });
            return ev.target.value;
          }}
        />
      </GridItem>
      <GridItem xs={12}>
        <SimpleInput
          upperCase
          title="Placa Vehicular"
          name="plate"
          value={form.plate}
          onBlur={ev => {
            setForm({ ...form, plate: ev.target.value });
            return ev.target.value;
          }}
        />
      </GridItem>
      <GridItem xs={12}>
        <SimpleInput
          upperCase
          title="Descripción"
          name="description"
          value={form.description}
          onBlur={ev => {
            setForm({ ...form, description: ev.target.value });
            return ev.target.value;
          }}
        />
      </GridItem>

      <GridItem xs={12}>
        <StreetSelector
          title="Domicilio"
          value={form.address}
          onChange={ev => {
            setForm({ ...form, address: ev.value });
          }}
          name="streetSelector"
        />
      </GridItem>
      <Button
        type="submit"
        fullWidth
        variant="contained"
        color="primary"
        className={"submit"}
        onClick={() => {
          if (form.address != "") {
            toggleModal(true);
          }
        }}
      >
        Guardar
      </Button>
      <ConditionalModal
        open={modal}
        style={{ width: "80%" }}
        title="Llama al hospedante"
        onSubmit={response => {
          if (response) {
            _willSubmit(form);
          }
          toggleModal(false);
        }}
      >
        <PhoneList zone={form.address} />
      </ConditionalModal>
    </PanelContainer>
  );
}
export function PhoneList({ zone }) {
  const [phones, setPhones] = useState([]);

  const loadPhones = async () => {
    if ((zone == "", zone == false)) {
      return false;
    }
    const fetch = new AuthFetch(`api/houses/${zone}/persons`);
    const house = (await fetch.get()).data;
    if (Object.values(house).length == 0) {
      setPhones(["Llama a administración"]);
      return false;
    }
    setPhones(JSON.parse(house.resident.phones));
  };

  useEffect(() => {
    loadPhones();
    // eslint-disable-next-line
  }, []);

  return (
    <ul className="phoneList">
      {phones.map((phone, key) => (
        <li key={key}>
          <GridContainer>
            <GridItem xs={2}>
              <FontIcon iconName={"fa-phone"} />
            </GridItem>
            <GridItem xs={10}>{phone}</GridItem>
          </GridContainer>
        </li>
      ))}
    </ul>
  );
}
