import React from "react";
import Avatar from "@material-ui/core/Avatar";
import CssBaseline from "@material-ui/core/CssBaseline";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { AjaxForm } from "../../components/Containers/AjaxForm";
import "./login.scss";
import { LoginManager } from "../../utils/LoginManager";

import logo from "assets/img/favicon.png";
import { SimpleInput } from "../../components/CustomInput/SimpleInput";
import { FaButton } from "../../components/CustomButtons/FaButton";
import { ActionInput } from "../../components/CustomInput/ActionInput";
import { FontIcon } from "../../components/Icons/FontIcon";

export class Login extends React.Component {
  state = { type: "password" };
  onSubmit(result) {
    const logged = this.props.onLogin;
    let token = new LoginManager().login(result).getToken();
    if (typeof logged !== "function") {
      throw new Error("on Login Props its needed");
    }
    return logged(token);
  }
  render() {
    const { type } = this.state;
    let typeState = type === "password";
    return (
      <div className="loginBackground">
        <main className={"login"}>
          <CssBaseline />
          <Paper className={"paper"}>
            <Avatar className={"avatar"}>
              <img src={logo} alt={"El manantial"} style={{ width: "100%" }} />
            </Avatar>
            <Typography
              style={{
                color: "white",
                textShadow: "1px 1px 1px black",
                textAlign: "center"
              }}
              component="h1"
              variant="h5"
            >
              El manantial
            </Typography>
            <AjaxForm
              autoload={false}
              path="/api/auth/login"
              method="post"
              submitted={this.onSubmit.bind(this)}
            >
              <SimpleInput
                title="Email"
                id="email"
                name="email"
                autoComplete="email"
                autoFocus
                style={{ color: "white" }}
                value=""
              />
              <ActionInput
                disableEnter
                icon={
                  <FontIcon
                    iconName={`fa-eye${typeState ? "" : "-slash"}`}
                    style={{ color: "white" }}
                  />
                }
                onClick={val => {
                  this.setState({
                    type: typeState ? "text" : "password"
                  });
                  return val;
                }}
                title="Contraseña "
                name="password"
                type={type}
                id="password"
                autoComplete="current-password"
                style={{ color: "white" }}
                value=""
              />
              <FaButton
                aria-label="Ingresar"
                type="submit"
                size="14px"
                icon="sign-in-alt"
                className={"submit"}
                style={{
                  color: "white",
                  textShadow: "1px 1px 1px black"
                }}
              >
                Ingresar
              </FaButton>
            </AjaxForm>
          </Paper>
        </main>
      </div>
    );
  }
}
