import React, { Component } from "react";
import { Persons } from "../../utils/Controller/Persons";
import { playAudio, optionalFn } from "../../core/helpers";
import { validAccess } from "assets/sound/validAccess.mp3";
import { AuthFetch } from "../../utils/AuthFetch";
import { UserCard } from "../Users/UserCard";
import { wait } from "assets/sound/wait.mp3";
import { AccessTimer } from "./utils/AccessTimer";
import { helpSound } from "assets/sound/help.mp3";
import swal from "sweetalert";
import Add from "@material-ui/icons/Add";
import Close from "@material-ui/icons/Close";
import { CustomModal } from "./../../components/CustomModal/CustomModal";

/**
 * Controla el proceso de acceso
 */
export class AccessPanel extends Component {
  state = {
    time: 1,
    status: ""
  };
  constructor(props) {
    super(props);
    this.state.time = props.door.seconds || 15;
  }
  /**
   * Cambia el estatus de acceso de ciertos usuarios.
   * @param int id
   */
  _setAccess({ type, id }) {
    console.log(type);
    switch (type) {
      case "Visitantes":
        new Persons(id).setAccess(false, { hash: true });
        break;
    }
  }
  /**
   * Otorga acceso al usuario
   * @param int personID
   * @param string type
   * @param int doorType
   * @param string address
   * @param {*} door
   */
  async grantAccess(personID, type, doorType, address, door) {
    //playAudio(validAccess);
    const fetch = new AuthFetch(`api/access/${personID}`);
    const access = await fetch.post({
      inside: doorType,
      door: door.id,
      houseCode: address
    });
    if (access.data.personID === personID) {
      this.openDoor();
    }
    this._setAccess({ id: personID, type });
  }
  async openDoor() {
    let door = this.props.door;
    const fetch = new AuthFetch(`api/doors/${door.id}/run`);
    let response = (await fetch.get()).data;
    if (response == false) {
      playAudio(helpSound);
      swal("Advertencia", `No se pudo abrir la puerta`, "error");
    }
  }
  render() {
    const {
      id,
      photo,
      fullname,
      type,
      doorType,
      door,
      address,
      onSave
    } = this.props;

    const { time } = this.state;
    return (
      <CustomModal
        open={this.props.open}
        onClose={async () => {
          await this.setState({ status: "cancel" });
        }}
      >
        <UserCard
          pic={photo}
          fullName={fullname}
          type={type}
          okIcon={<Add />}
          onOk={async () => {
            await this.setState({ status: "cancel" });
            this.grantAccess(id, type, doorType, address, door);
            optionalFn(onSave)();
          }}
          onCancel={async () => {
            await this.setState({ status: "cancel" });
          }}
          noIcon={<Close />}
          style={{
            minWidth: "20rem",
            minHeight: "50vh",
            paddingTop: "1rem"
          }}
        >
          {`${time} segundos restantes`}
        </UserCard>
      </CustomModal>
    );
  }
  componentDidMount() {
    playAudio(wait);
    this.startTimer();
  }
  startTimer = () => {
    let timer = new AccessTimer(this.state.time);
    timer
      .start()
      .each(time => {
        console.log(this.state.status);
        if (this.state.status == "cancel") {
          timer.cancel(() => {
            optionalFn(this.props.onClose)();
          });
        }
        this.setState({ time });
      })
      .onTimesOut(() => {
        optionalFn(this.props.onClose)();
        optionalFn(this.props.onSave)();

        const { id, type, doorType, door, address } = this.props;
        this.grantAccess(id, type, doorType, address, door);
      });
  };
}
