import React, { useState } from "react";
import { CustomModal } from "components/CustomModal/CustomModal";
import { GenericSelector } from "../../components/CustomInput/GenericSelector";
import Card from "components/Card/Card.jsx";
import { Persons } from "../../utils/Controller/Persons";
import { optionalFn } from "../../core/helpers";
import { FaButton } from "../../components/CustomButtons/FaButton";

/**
 * Genera un boton que permite cambiar el tipo de usuario.
 * @param {person,onTypeChange} props
 */
export function UserTypeSelector({ person, onTypeChange }) {
  const [personData, setPersonData] = useState({ ...person, modal: false });
  const send = async ev => {
    const fetch = new Persons(person.id);
    let type = { type: ev.value };
    await fetch.set({
      limitedAccess:
        ev.value !== "Invitado"
          ? new Date(null).toISOString().substring(0, 10)
          : new Date().toISOString().substring(0, 10),
      ...type
    });
    setPersonData({ personData, ...type });
    optionalFn(onTypeChange)(type);
  };
  return (
    <>
      <FaButton
        onClick={() => {
          setPersonData({ ...personData, modal: true });
        }}
        size=".8rem"
        icon="sync-alt"
      >
        {" "}
        {personData.type}
      </FaButton>
      <CustomModal open={personData.modal}>
        <Card
          style={{
            width: "16rem",
            padding: "1rem"
          }}
        >
          <label for="typeSelector">
            El usuario {person.fullname} sera transferido al perfil:
          </label>
          <GenericSelector
            name="typeSelector"
            value={personData.type}
            onChange={send}
          >
            <option value="Residente">Residente</option>
            <option value="Invitado">Invitado</option>
          </GenericSelector>
        </Card>
      </CustomModal>
    </>
  );
}
