import React, { Component } from "react";
import { AuthFetch } from "../../utils/AuthFetch";
import { CustomModal } from "../../components/CustomModal/CustomModal";
import { PersonsContainer } from "../Forms/PersonsContainer";
import QR from "../../components/QR/Qr";
import { CustomSwitchAccess } from "../../components/CustomSwitchAccess/CustomSwitchAccess";
import { List, ListItemText, IconButton } from "@material-ui/core";
import { ListItem } from "@material-ui/core";
import Avatar from "@material-ui/core/Avatar";
import { FontIcon } from "../../components/Icons/FontIcon";
import { UserTypeSelector } from "./UserTypeSelector";
import { PermissionWall } from "../../components/FilterWall/PermissionWall";
import { optionalFn } from "../../core/helpers";
import { insteadIMG } from "../../utils/Utils";
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem.jsx";
import { FaButton } from "./../../components/CustomButtons/FaButton";

/* eslint eqeqeq: 0*/

export class ResidentsContainer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      habitants: [],
      openQr: false,
      hash: "",
      personID: null,
      modal: props.modal
    };
  }
  componentDidMount() {
    this.loadHabitants(this.props.code);
  }
  async componentWillReceiveProps(nextProps) {
    let value = nextProps.code;
    let modal = nextProps.modal;
    if (value != this.props.code) {
      this.loadHabitants(value);
    }
    if (modal != this.props.modal) {
      this.setState({ modal });
    }
  }
  async loadHabitants(houseCode) {
    const fetch = new AuthFetch("api/residents");
    const habitants = (await fetch.get({ houseCode })).data;
    this.setState({ habitants, modal: false, personID: null });
  }

  async addResident(inputs) {
    const onAddResident = this.props.onAddResident;
    const houseCode = this.props.code;

    if (houseCode == "") {
      return false;
    }
    if (this.state.personID != null) {
      this.loadHabitants(houseCode);
      return false;
    }
    const fetch = new AuthFetch("api/residents/");
    await fetch.post({ houseCode, personID: inputs.id });
    this.loadHabitants(houseCode);
    optionalFn(onAddResident)(houseCode);
    this.handleClose();
  }
  async reloadQr(id) {
    const houseCode = this.props.code;
    const fetch = new AuthFetch(`api/persons/${id}`);
    await fetch.put({ hash: true });
    this.loadHabitants(houseCode);
  }
  handleClose = () => {
    this.setState({
      openQr: false,
      hash: "",
      personID: null,
      modal: false
    });
    optionalFn(this.props.onClose)(this.props.code);
  };
  render = () => {
    const { type } = this.props;
    let { habitants, openQr, hash, personID, modal } = this.state;
    habitants = habitants.filter(item => {
      return item.person.type == type;
    });

    return (
      <List style={{ width: "80%" }}>
        {habitants.map((item, key) => (
          <ListItem
            key={key}
            divider
            style={{
              borderRadius: "9px",
              margin: "0px 3px"
            }}
          >
            <ListItemText
              primary={
                <>
                  <GridContainer>
                    <GridItem xs={3}>
                      <Avatar
                        alt={item.person.fullname}
                        src={insteadIMG(item.person.photo)}
                      />
                    </GridItem>
                    <GridItem xs={9}>
                      <span
                        style={{
                          paddingTop: "8px",
                          textAlign: "center",
                          display: "justify"
                        }}
                      >
                        {item.person.fullname.toUpperCase()}
                      </span>
                    </GridItem>
                  </GridContainer>
                </>
              }
              secondary={
                <React.Fragment>
                  <IconButton
                    color="primary"
                    onClick={() => {
                      this.setState({
                        openQr: true,
                        hash: item.person.hash,
                        personID: item.personID
                      });
                    }}
                  >
                    <FontIcon iconName="fa-qrcode" />
                  </IconButton>
                  <CustomSwitchAccess
                    id={item.personID}
                    hasAccess={item.person.hasAccess}
                    expirationDate={item.person.limitedAccess}
                  />
                  <PermissionWall permission="usuarios">
                    <FaButton
                      key={item.id + "Fa"}
                      size="12px"
                      onClick={() => {
                        this.setState({ personID: item.personID, modal: true });
                      }}
                      icon="edit"
                    />
                  </PermissionWall>
                  <PermissionWall permission="usuarios">
                    <UserTypeSelector person={item.person} />
                  </PermissionWall>
                  {item.person.limitedAccess || "Vitalicia"}
                </React.Fragment>
              }
            />
          </ListItem>
        ))}
        <CustomModal open={modal} onClose={this.handleClose}>
          <PersonsContainer
            edit={personID}
            type={type}
            submission={this.addResident.bind(this)}
          />
        </CustomModal>
        <CustomModal open={openQr} onClose={this.handleClose}>
          <QR
            data={JSON.stringify({
              h: hash
            })}
            onReload={() => {
              this.reloadQr(personID);
            }}
          />
        </CustomModal>
      </List>
    );
  };
}
