import React, { Component } from "react";
import { AuthFetch } from "./../../utils/AuthFetch";
import { DataTable } from "./../../components/Table/DataTable";
export class HistoryAccess extends Component {
  state = {
    access: []
  };
  constructor(props) {
    super(props);
    this.loadAccess();
  }
  /**
   * Carga todos los accesos de la base de datos.
   */
  async loadAccess() {
    const { by, value } = this.props;
    let access = (await this.setByType(by, value)).data;
    access = access.map(item => {
      return [
        item.fullname,
        item.inside ? "Entrada" : "Salida",
        new Date(item.updated.date).toLocaleDateString("es-MX", {
          year: "numeric",
          month: "numeric",
          day: "numeric",
          hour: "2-digit",
          minute: "2-digit"
        })
      ];
    });
    this.setState({ access });
  }
  setByType(type, value) {
    let response;
    let path = "";
    switch (type) {
      case "code":
        path = `api/access/house/${value}`;
        response = this.fetchAccess(path);

        break;
      case "person":
        path = `api/access/`;
        response = this.fetchAccess(path, { personID: value });

        break;
      default:
        path = `api/access/`;
        response = this.fetchAccess(path);

        break;
    }
    return response;
  }
  fetchAccess(path, args = {}) {
    let fetch = new AuthFetch(path);
    return fetch.get(args);
  }
  render() {
    const { access } = this.state;
    return (
      <DataTable
        hideButton
        title="Accesos"
        subtitle="Historial de accesos"
        titles={["Nombre", "Estatus", "Fecha"]}
        content={access}
      />
    );
  }
}
