import React, { useState, useEffect } from "react";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import { SimpleInput } from "../../components/CustomInput/SimpleInput";
import GridItem from "components/Grid/GridItem.jsx";
import { AuthFetch } from "../../utils/AuthFetch";
/* eslint eqeqeq: 0*/
export function RateList() {
  const [maintenance, setMaintenance] = useState(0.0);
  const [recharges, setRecharges] = useState("");
  const loadRates = async () => {
    const fetch = new AuthFetch("api/rates");
    const rates = (await fetch.get()).data;
    if (rates.length == 0) {
      return false;
    }
    setMaintenance(rates[0].rate);
    setRecharges(rates[1].rate);
  };
  const setRates = (id, rate) => {
    const fetch = new AuthFetch(`api/rates/${id}`);
    fetch.put({ rate });
  };
  useEffect(() => {
    loadRates();
  }, []);
  return (
    <PanelContainer
      title="Tarifas"
      subtitle="Permite editar las tarifas y recargos"
    >
      <GridItem xs={12}>
        <SimpleInput
          title="Mantenimiento"
          type="number"
          value={maintenance}
          onBlur={ev => setRates(1, ev.target.value)}
        />
      </GridItem>
      <GridItem xs={12}>
        <SimpleInput
          title="Recargos (%)"
          type="number"
          value={recharges}
          onBlur={ev => setRates(2, ev.target.value)}
        />
      </GridItem>
    </PanelContainer>
  );
}
