import React, { Component } from "react";
import { PanelContainer } from "./../../components/Panel/PanelContainer";
import { ResidentsContainer } from "./../Users/ResidentsContainer";
import { LoginManager } from "./../../utils/LoginManager";
import { AuthFetch } from "./../../utils/AuthFetch";
import { ProfileView } from "./../Users/ProfileView";
import { PermissionWall } from "./../../components/FilterWall/PermissionWall";
import { HistoryAccess } from "../Access/HistoryAccess";
import GridContainer from "components/Grid/GridContainer";
import GridItem from "components/Grid/GridItem.jsx";
export default class Dashboard extends Component {
  state = { code: "", openGuestModal: false, hasPermission: false };

  async getCode() {
    const loginManager = new LoginManager();
    const me = loginManager.getToken();
    let permission = loginManager.hasPermission("invitados");
    if (permission === false) {
      return permission;
    }
    const fetch = new AuthFetch("api/houses");
    const house = await fetch.get({ ownerID: me.personID });
    if (house.data.length <= 0) {
      return false;
    }
    const code = `${house.data[0].zone}-${house.data[0].houseNumber}`;
    this.setState({ code });
    return `${house.data[0].zone}-${house.data[0].houseNumber}`;
  }
  componentDidMount() {
    this.getCode();
  }
  render() {
    const { code, openGuestModal } = this.state;
    const loginManager = new LoginManager();
    const me = loginManager.getToken();
    return (
      <React.Fragment>
        <GridContainer>
          <GridItem xs={12} lg={12}>
            <ProfileView />
            <PermissionWall permission="invitados">
              <GridContainer>
                <GridItem xs={12} lg={6}>
                  <PanelContainer title="Habitantes" subtitle=" ">
                    <ResidentsContainer
                      code={code}
                      modal={false}
                      type="Residente"
                    />
                  </PanelContainer>
                </GridItem>
                <GridItem xs={12} lg={6}>
                  <PanelContainer
                    title="Invitados"
                    subtitle=" "
                    onAdd={() => {
                      this.setState({
                        openGuestModal: true
                      });
                    }}
                  >
                    <ResidentsContainer
                      code={code}
                      modal={openGuestModal}
                      type="Invitado"
                      onClose={() => {
                        this.setState({
                          openGuestModal: false
                        });
                      }}
                    />
                  </PanelContainer>
                </GridItem>
              </GridContainer>
            </PermissionWall>
          </GridItem>
          <GridItem xs={12} lg={12}>
            <HistoryAccess by="person" value={me.personID} />
          </GridItem>
        </GridContainer>
      </React.Fragment>
    );
  }
}
