import { Persons } from "./../../../utils/Controller/Persons";
export function hasAccess({ id, hasAccess, limitedAccess }) {
  if (limitedAccess == null) {
    return hasAccess;
  }
  let time = new Date(limitedAccess).getTime();
  const now = new Date().getTime();
  if (time > now && hasAccess) {
    return true;
  }
  if (time < now) {
    new Persons(id).setAccess(false, { hash: true });
  }
  return false;
}
