import { AuthFetch } from "../../../utils/AuthFetch";
import swal from "sweetalert";
import { isObjectEmpty } from "./../../../core/helpers";

/*eslint eqeqeq: 0*/
export function calcTotals(data) {
  let fullTotal = 0;
  let totals = {};
  for (let item of data) {
    const total =
      item.type === "payment"
        ? parseFloat(item.import) * -1
        : parseFloat(item.import);
    totals[item.type] = (totals[item.type] || 0) + total;
    fullTotal += total;
  }
  totals["total"] = fullTotal;
  return totals;
}
/**
 *
 * @param integer houseID
 * @param float value
 */
export async function pay(
  houseID,
  value,
  rfc = "XAXX010101000",
  paymentMethod = 99
) {
  let financeID = 0;
  const finances = new AuthFetch("api/finances");
  let data = (await finances.post({
    description: "Pago de adeudo",
    amount: value,
    conceptID: "maintenance fees",
    rfc,
    paymentMethod
  })).data;
  financeID = data.id;
  const charges = new AuthFetch(`api/charge/`);
  await charges.post({
    type: "payment",
    houseID: houseID,
    import: value,
    financeID
  });
  return data;
}
/**
 * En cargos no reconocidos define el balance.
 * @param int id
 * @param {float,float,float} param1
 */
export function setBalance(id, { amount, balance, payments }) {
  const fetch = new AuthFetch(`api/payments/unrecognized/${id}`);
  balance = parseFloat(balance) + parseFloat(payments);
  let total = amount - balance;
  if (balance < 0) {
    return false;
  }
  fetch.put({ balance });
  return total;
}
/**
 * determina si el id tiene una deuda pendiente como residente o propietario de la vivienda
 * @param int id
 */
export async function itsDebtor(id) {
  const fetch = new AuthFetch(`api/charge/${id}/unpaid`);
  const data = (await fetch.get()).data;
  if (!isObjectEmpty(data)) {
    await swal({
      title: "Advertencia",
      text: `Hay un adeudo pendiente de pago, favor de pasar a administración.`,
      timer: 3000,
      buttons: false
    });
  }

  return data;
}
