import React, { useState } from "react";
import { CustomModal } from "components/CustomModal/CustomModal";
import Card from "components/Card/Card.jsx";
import CardBody from "components/Card/CardBody.jsx";
import { StreetSelector } from "../Houses/StreetSelector";
import { Persons } from "../../utils/Controller/Persons";
import { AuthFetch } from "../../utils/AuthFetch";
import { optionalFn } from "../../core/helpers";
import Button from "components/CustomButtons/Button.jsx";
import { ConditionalModal } from "../../components/CustomModal/ConditionalModal";
import { PhoneList } from "./VisitorsForm";
export function ModalStreet({ personId, onChange, onClose, ...rest }) {
  const [street, setStreet] = useState();
  const [modal, toggleModal] = useState(false);
  const saveAddress = () => {
    const fetch = new AuthFetch("api/residents/");
    fetch.post({
      houseCode: street,
      personID: personId
    });
    new Persons(personId)
      .setAccess(true, {
        hash: true,
        limitedAccess: new Date().toISOString().substring(0, 10)
      })
      .then(person => {
        optionalFn(onChange)(person.data, street);
      });
  };
  return (
    <CustomModal onClose={onClose} {...rest}>
      <Card>
        <CardBody>
          <StreetSelector
            title="Selecciona domicilio a donde se dirige"
            value={street}
            name="really"
            onChange={ev => {
              setStreet(ev.value);
            }}
          />
          <Button
            fullWidth
            color="primary"
            onClick={() => {
              toggleModal(true);
            }}
          >
            Guardar
          </Button>
        </CardBody>
      </Card>

      <ConditionalModal
        open={modal}
        style={{ width: "80%" }}
        title="Llama al hospedante"
        onSubmit={response => {
          if (response) {
            saveAddress();
          }
          toggleModal(false);
        }}
      >
        <PhoneList zone={street} />
      </ConditionalModal>
    </CustomModal>
  );
}
