import React, { Component, useState } from "react";
import { AjaxForm } from "../../components/Containers/AjaxForm";
import { SimpleInput } from "../../components/CustomInput/SimpleInput";
import { GenericSelector } from "../../components/CustomInput/GenericSelector";
import GridItem from "components/Grid/GridItem.jsx";
import { Translator } from "../../utils/Translator";
import Button from "@material-ui/core/Button";
import { ConditionalWall } from "../../components/FilterWall/ConditionalWall";
import {
  validInputDate,
  numberToMoney,
  Helpers,
  optionalFn
} from "../../core/helpers";
import { FancyCheckBox } from "../../components/CustomInput/FancyCheckBox";
import { ActionInput } from "../../components/CustomInput/ActionInput";
import { FontIcon } from "../../components/Icons/FontIcon";
import GridContainer from "components/Grid/GridContainer.jsx";
import { AsyncSelector } from "../../components/CustomInput/AsyncSelector";
import { AuthFetch } from "../../utils/AuthFetch";
import { CustomModal } from "../../components/CustomModal/CustomModal";
import { FaButton } from "../../components/CustomButtons/FaButton";
import { PanelContainer } from "../../components/Panel/PanelContainer";
/* eslint eqeqeq: 0*/
export class ServiceForm extends Component {
  state = {
    id: "",
    service: "",
    price: "",
    recharge: "",
    rechargeTimeLimitOnDays: "",
    iteration: "",
    iterationType: "",
    applyTo: "",
    chargeType: "unitary",
    startDate: validInputDate(),
    willRecharge: false,
    iterationLimit: 1,
    agreement: false
  };
  constructor(props) {
    super(props);
    this.state.id = props.id || "";
  }
  render() {
    const {
      id,
      service,
      price,
      agreement,
      iteration,
      rechargeTimeLimitOnDays,
      willRecharge,
      recharge,
      iterationType,
      applyTo,
      chargeType,
      startDate,
      iterationLimit
    } = this.state;
    return (
      <AjaxForm
        style={{ margin: "0 11rem" }}
        path={`api/services/${id}`}
        autoload={id != ""}
        method={id != "" ? "PUT" : "POST"}
        willSubmit={inputs => {
          inputs.agreement = agreement * 1;
          return inputs;
        }}
        getter={ev => {
          const service = ev.data;
          service.iterationType = service.iterationType || "";
          service.willRecharge = service.recharge > 0;

          this.setState(service);
        }}
        submitted={this.props.onSave}
      >
        <GridContainer justify="center" alignContent="center">
          <GridItem xs={12}>
            <SimpleInput
              title="Servicio"
              type="text"
              name="service"
              upperCase
              required
              value={service}
            />
          </GridItem>
          <GridItem xs={12}>
            <GenericSelector
              title="Tipo de cobro"
              required
              name="chargeType"
              value={chargeType}
              onChange={ev => {
                this.setState({ chargeType: ev.value });
              }}
            >
              <option value="meters">Por metros</option>
              <option value="unitary">Unitario</option>
            </GenericSelector>
          </GridItem>
          <GridItem xs={12}>
            <SimpleInput
              required
              title={`Precio ${
                chargeType == "meters" ? "por metro" : "unitario"
              }`}
              type="number"
              name="price"
              value={price}
            />
          </GridItem>
          <GridItem xs={12}>
            <GenericSelector
              required
              title="Aplicar a"
              name="applyTo"
              value={applyTo}
              onChange={ev => {
                this.setState({
                  applyTo: ev.value
                });
              }}
            >
              <option value="one">Terreno</option>
              <option value="all">Todos</option>
            </GenericSelector>
          </GridItem>

          <GridItem xs={12}>
            <SimpleInput
              title="Programar para"
              type="date"
              name="startDate"
              value={startDate}
            />
          </GridItem>
          <GridItem xs={12}>
            <GenericSelector
              title="¿Repetir?"
              name="iterationType"
              value={iterationType}
              onChange={ev => {
                this.setState({ iterationType: ev.value });
              }}
            >
              <option value="">No repetir</option>
              <option value="day">Días</option>
              <option value="month">Meses</option>
            </GenericSelector>
          </GridItem>
          <ConditionalWall conditional={iterationType != ""}>
            <GridItem xs={12}>
              <SimpleInput
                title={`Repetir cada (${new Translator(iterationType).get()})`}
                type="number"
                name="iteration"
                value={iteration}
              />
            </GridItem>
            <GridItem xs={12}>
              <ActionInput
                title="Veces a repetir"
                type={iterationLimit != "infinite" ? "number" : "text"}
                value={
                  iterationLimit == "infinite" ? "infinito" : iterationLimit
                }
                name="iterationLimit"
                onBlur={ev => {
                  const value = ev.target.value;
                  this.setState({ iterationLimit: value });
                }}
                onClick={() => {
                  this.setState({ iterationLimit: "infinite" });
                }}
                icon={<FontIcon iconName="fa-infinity" />}
              />
            </GridItem>
          </ConditionalWall>
          <GridItem xs={12}>
            <FancyCheckBox
              title="Acuerdos"
              value={agreement}
              onChange={() => {
                this.setState({ agreement: !agreement });
              }}
            >
              ¿Puede generar acuerdos?
            </FancyCheckBox>
          </GridItem>
          <GridItem xs={12}>
            <FancyCheckBox
              value={willRecharge}
              title="Recargos"
              onChange={() => {
                this.setState({ willRecharge: !willRecharge });
              }}
            >
              ¿Puede generar recargos?
            </FancyCheckBox>
          </GridItem>
          <ConditionalWall conditional={willRecharge}>
            <GridItem xs={12}>
              <SimpleInput
                title="Recarga (%)"
                name="recharge"
                value={recharge}
                type="number"
              />
            </GridItem>
            <GridItem xs={12}>
              <SimpleInput
                title="Periodo de gracia (días)"
                name="rechargeTimeLimitOnDays"
                value={rechargeTimeLimitOnDays}
                type="number"
              />
            </GridItem>
          </ConditionalWall>
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={"submit"}
          >
            {new Translator("Save").get()}
          </Button>
        </GridContainer>
      </AjaxForm>
    );
  }
}
export function ServiceSelector({ onChange }) {
  return (
    <AsyncSelector
      title="Conceptos aplicables"
      path="api/services"
      args={{ applyTo: "one" }}
      onChange={onChange}
      formatOptions={items => {
        const { id, service, price, startDate } = items;
        const status =
          startDate == null ? "Inmediato" : `Programado: ${startDate}`;
        return {
          value: id,
          title: `${service} (${numberToMoney(price)}) ${status}`
        };
      }}
    />
  );
}
export function LinkService({ houseID, onSave }) {
  const [serviceID, setID] = useState();
  const [date, setDate] = useState(validInputDate());
  const sendServiceLink = async () => {
    const fetch = new AuthFetch(`api/services/link/${houseID}`);
    await fetch.post({ serviceID, startDate: date });
    optionalFn(onSave)({ serviceID, startDate: date });
  };
  return (
    <>
      <GridContainer justify="center" alignContent="center">
        <GridItem xs={12}>
          <ServiceSelector
            value={serviceID}
            onChange={(ev, content) => {
              const helpers = new Helpers();
              const service = helpers.searchByKey(content, "id", ev.value)[0];
              setDate(validInputDate(service.startDate));
              setID(ev.value);
            }}
          />
        </GridItem>
        <GridItem xs={12}>
          <SimpleInput
            title="Inicia el"
            type="date"
            onBlur={({ target }) => {
              setDate(target.value);
            }}
            value={validInputDate(date)}
          />
        </GridItem>
        <GridItem xs={12}>
          <Button
            color="primary"
            fullWidth
            variant="contained"
            onClick={() => {
              sendServiceLink();
            }}
          >
            Guardar
          </Button>
        </GridItem>
      </GridContainer>
    </>
  );
}
export function ModalService({ houseID, onSave, ...rest }) {
  const [open, setModal] = useState(false);
  return (
    <>
      <FaButton
        icon="money-bill-wave-alt"
        title="Añadir cargo"
        onClick={() => {
          setModal(!open);
        }}
      />
      <CustomModal open={open}>
        <PanelContainer title={"Añadir cargo"}>
          <LinkService
            houseID={houseID}
            {...rest}
            onSave={() => {
              setModal(!open);
              optionalFn(onSave)();
            }}
          />
        </PanelContainer>
      </CustomModal>
    </>
  );
}
