import React, { useState, useEffect } from "react";
import { AuthFetch } from "./../../utils/AuthFetch";
import { GenericSelector } from "./../../components/CustomInput/GenericSelector";

export function StreetSelector({ zone, name, ...rest }) {
  const [options, setOptions] = useState([]);
  const loadZones = async () => {
    const fetch = new AuthFetch("/api/address");
    const filter = zone ? { zone } : {};
    const data = (await fetch.get(filter)).data;
    setOptions(data);
  };
  useEffect(() => {
    loadZones();
    // eslint-disable-next-line
  }, []);
  return (
    <GenericSelector name={name || "streetSelector"} {...rest}>
      {options.map((item, key) => {
        return (
          <option key={key} value={`${item.zone}-${item.number}`}>
            {item.address}
          </option>
        );
      })}
    </GenericSelector>
  );
}
