import React, { Component, useState } from "react";
import { AjaxForm } from "../../components/Containers/AjaxForm";
import { DataTable } from "../../components/Table/DataTable";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import { AuthFetch } from "../../utils/AuthFetch";
import GridItem from "components/Grid/GridItem.jsx";
import { SimpleInput } from "../../components/CustomInput/SimpleInput";
import { Button } from "@material-ui/core";
import { Translator } from "../../utils/Translator";
import { FaButton } from "../../components/CustomButtons/FaButton";
import swal from "sweetalert";
/* eslint eqeqeq: 0*/
export class AddressContainer extends Component {
  state = {
    address: [],
    id: ""
  };
  async deleteAddress(id) {
    const fetch = new AuthFetch(`api/address/${id}`);
    let response = await swal({
      title: "Advertencia",
      text: `Estas a punto de eliminar una dirección, esto no puede deshacerse, ¿Deseas continuar?.`,
      showCancelButton: false,
      type: "input",
      dangerMode: true,
      buttons: { cancel: "No", confirm: "Eliminar" },
      icon: "warning"
    });
    if (response) {
      await fetch.delete();
    }
    this.loadAddress();
  }
  async loadAddress() {
    const fetch = new AuthFetch("api/address");
    let data = (await fetch.get()).data;
    data = data.map(item => {
      return [
        item.id,
        item.zone,
        item.number,
        item.address,
        item.created_at,
        <>
          <FaButton
            icon="edit"
            size="12px"
            onClick={() => {
              this.setState({ id: item.id, modalOpen: true });
            }}
          />
          <FaButton
            icon="trash"
            size="12px"
            style={{ color: "red" }}
            onClick={() => {
              this.deleteAddress(item.id);
            }}
          />
        </>
      ];
    });
    this.setState({
      address: data,
      modalOpen: false,
      id: ""
    });
  }
  _handleClose() {
    this.loadAddress();
  }
  componentDidMount() {
    this.loadAddress();
  }
  render() {
    const { address, modalOpen, id } = this.state;
    return (
      <DataTable
        title="Domicilios"
        subtitle="Lista de domicilios"
        titles={["ID", "Coto", "Lote", "Domicilio", "Fecha", "Acciones"]}
        content={address}
        modalOpen={modalOpen}
        onClose={() => {
          this.setState({ modalOpen: false, id: null });
        }}
        inModal={
          <AddressRegister
            id={id}
            onSubmit={() => {
              this._handleClose();
            }}
          />
        }
      />
    );
  }
}
export function AddressRegister({ id, onSubmit }) {
  const [domicile, setAddress] = useState({
    zone: "",
    number: "",
    address: ""
  });
  const { zone, number, address } = domicile;
  const load = id != "";
  return (
    <AjaxForm
      autoload={load}
      path={`api/address/${id}`}
      method={load ? "put" : "post"}
      submitted={onSubmit}
      willSubmit={inputs => {
        return inputs;
      }}
      getter={data => {
        setAddress(data.data);
      }}
    >
      <PanelContainer title="Registra un nuevo domicilio" subtitle=" ">
        <GridItem xs={12} md={6}>
          <SimpleInput title="Coto" name="zone" value={zone} />
        </GridItem>
        <GridItem xs={12} md={6}>
          <SimpleInput title="Lote" name="number" value={number} />
        </GridItem>
        <GridItem xs={12}>
          <SimpleInput title="Calle y número" name="address" value={address} />
        </GridItem>

        <Button
          type="submit"
          fullWidth
          variant="contained"
          color="primary"
          className={"submit"}
        >
          {new Translator("Save").get()}
        </Button>
      </PanelContainer>
    </AjaxForm>
  );
}
