import React, { Component } from "react";
import { AuthFetch } from "../../utils/AuthFetch";
import { DataTable } from "../../components/Table/DataTable";
import { FinancesForm } from "./FinancesForm";
import swal from "sweetalert";
import { numberToMoney } from "../../core/helpers";
import { FinancesCore, getPaymentMethodByKey } from "./CoreFinances";
import { CardList } from "../../components/Lists/CardList";
import { RateList } from "./RatesList";
import { Translator } from "../../utils/Translator";
export class FinancesContainer extends Component {
  state = {
    id: null,
    finances: [],
    openForm: false,
    account: {}
  };
  async loadFinances() {
    const fetch = new AuthFetch("api/finances");
    let finances = (await fetch.get()).data;
    const finTech = new FinancesCore(finances);
    finances = finances.map(item => [
      item.rfc,
      item.description,
      new Translator(item.concept.concept).get(),
      numberToMoney(item.amount),
      getPaymentMethodByKey(item.paymentMethod),
      item.employee.fullname,
      new Date(item.created_at.date).toLocaleDateString("es-MX", {
        year: "numeric",
        month: "numeric",
        day: "numeric",
        hour: "2-digit",
        minute: "2-digit"
      })
    ]);

    this.setState({
      finances,
      id: null,
      openForm: false,
      account: finTech.getAccount()
    });
  }
  componentDidMount() {
    this.loadFinances();
  }
  _handleClose() {
    this.setState({ id: null, openForm: false });
  }
  render() {
    const { finances, openForm, account } = this.state;
    return (
      <>
        <CardList
          items={account}
          itemsByLine="3"
          modifier={value => {
            return (
              <center>
                <h4>{numberToMoney(value)}</h4>
              </center>
            );
          }}
        />
        <RateList />
        <hr />
        <DataTable
          title="Finanzas"
          subtitle="Movimientos financieros"
          titles={[
            "R.F.C",
            "ID",
            "Concepto",
            "Precio",
            "Método de pago",
            "Generado por",
            "Generada el"
          ]}
          open={openForm}
          content={finances}
          onClose={this._handleClose.bind(this)}
          inModal={
            <FinancesForm
              submission={() => {
                swal("Excelente", "Movimiento generado", "success");
                this.loadFinances();
              }}
            />
          }
        />
      </>
    );
  }
}
