import React, { useState } from "react";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import { SimpleInput } from "../../components/CustomInput/SimpleInput";
import Button from "components/CustomButtons/Button.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import { ConditionalWall } from "../../components/FilterWall/ConditionalWall";
import { numberToMoney, optionalFn } from "../../core/helpers";
import { PaymentMethodSelector } from "./PaymentMethodSelector";
import { UnrecognizedPaymentSelector } from "./ UnrecognizedPayments";
import { FancyCheckBox } from "../../components/CustomInput/FancyCheckBox";
import { pay, setBalance } from "./core/FinancesUtils";

/* eslint eqeqeq: 0*/

export function PayInput({ total, id, onChange }) {
  const [value, setValue] = useState("");
  const [paymentMethod, setMethod] = useState("99");
  const [showSelector, toggleSelector] = useState(false);
  const [payment, setPayment] = useState({});
  const diff = numberToMoney(value - total);
  const addPayment = async () => {
    const { amount, balance } = payment;
    const val = showSelector ? amount : value;
    await pay(id, val, null, paymentMethod);
    if (showSelector)
      setBalance(payment.id, { amount, balance, payments: val });
    optionalFn(onChange)(value);
  };
  return (
    <PanelContainer
      title={id == 0 ? "Pago no identificado" : "Pago de adeudo"}
      subtitle=" "
    >
      <ConditionalWall conditional={id != 0}>
        <p>
          Existe un {total > 0 ? "adeudo por" : "saldo a favor por "} el total
          de: <strong>{numberToMoney(total)}</strong>
        </p>
      </ConditionalWall>

      <GridItem xs={12}>
        <PaymentMethodSelector
          onChange={value => {
            setMethod(value);
          }}
          required
        />
      </GridItem>
      <ConditionalWall conditional={id != 0}>
        <GridItem xs={12}>
          ¿Utilizar Pagos no reconocidos?
          <FancyCheckBox
            onChange={ev => {
              toggleSelector(ev);
            }}
          />
        </GridItem>
      </ConditionalWall>

      <GridItem xs={12}>
        {showSelector ? (
          <UnrecognizedPaymentSelector
            onChange={ev => {
              setPayment(ev);
            }}
          />
        ) : (
          <SimpleInput
            title="¿Cuanto desea pagar?"
            value={value}
            type="number"
            onBlur={ev => {
              setValue(ev.target.value);
              return ev.target.value;
            }}
          />
        )}

        <ConditionalWall conditional={value > total && id > 0}>
          <p>
            Hay un excedente de <strong>{diff}</strong>
          </p>
          <p>El excedente sera asignado como prepago.</p>
        </ConditionalWall>
      </GridItem>
      <Button
        fullWidth
        variant="contained"
        color="primary"
        onClick={addPayment}
      >
        Pagar
      </Button>
    </PanelContainer>
  );
}
