import React from "react";
import { GenericSelector } from "../../components/CustomInput/GenericSelector";
export function ZoneList({ onChange, value, placeholder }) {
  return (
    <GenericSelector
      placeholder={placeholder || "Filtrar por Coto"}
      name="zone"
      onChange={onChange}
      value={value}
    >
      <option value={null}>Todos</option>
      <option value="I">COTO I - FRESNO</option>
      <option value="II">COTO II - ARROYO</option>
      <option value="III">COTO III - BOSQUE</option>
      <option value="IV">COTO IV - ROBLE</option>
      <option value="V">COTO V - PINO</option>
      <option value="VI">COTO VI - OLMO</option>
      <option value="VII">COTO VII - SAUCE</option>
      <option value="VIII">COTO VIII - ABEDULES</option>
      <option value="IX">COTO IX -ALAMO</option>
      <option value="X">COTO X - MANANTIAL</option>
      <option value="XI">COTO XI - CEDROS</option>
    </GenericSelector>
  );
}
