import React, { Component } from "react";
import { ServiceForm } from "./ServicesComponents";
import { AsyncDataTable } from "../../components/Table/AsycnDataTable";
import { localeDate, numberToMoney } from "../../core/helpers";
import { Translator } from "../../utils/Translator";
import { FaButton } from "../../components/CustomButtons/FaButton";

/* eslint eqeqeq: 0*/
export class ServiceContainer extends Component {
  state = {
    id: "",
    open: false
  };
  render() {
    const { id, open } = this.state;
    return (
      <>
        <AsyncDataTable
          title="Lista de servicios"
          subtitle="Lista general de los servicios y precios a cobrar"
          inModal={
            <ServiceForm
              id={id}
              onSave={() => {
                this.setState({ id: "", open: false });
              }}
            />
          }
          path="api/services"
          modalOpen={open}
          onClose={() => {
            this.setState({ id: "", open: false });
          }}
          titles={{
            service: "Servicio",
            price: "Precio",
            chargeType: "Tipo",
            applyTo: "Cobro a",
            iteration: "Cada",
            iterationLimit: "Repetir",
            startDate: "Inicia el",
            actions: "Acciones"
          }}
          formatContent={item => {
            let times =
              item.iteration + " " + new Translator(item.iterationType).get();
            times = item.iteration == 0 ? "No repite" : times;
            let repeats =
              item.iterationLimit != "infinite"
                ? item.iterationLimit - item.iterationsUsed + " veces"
                : "Infinito";
            return [
              item.service,
              numberToMoney(item.price),
              new Translator(item.chargeType).get(),
              new Translator(item.applyTo).get(),
              times,
              repeats,
              localeDate(item.startDate),
              <>
                <FaButton
                  icon="edit"
                  title="modificar servicio"
                  onClick={() => {
                    this.setState({ id: item.id, open: true });
                  }}
                />
              </>
            ];
          }}
        ></AsyncDataTable>
      </>
    );
  }
}
