import React, { useState } from "react";
import { AuthFetch } from "../../utils/AuthFetch";
import { FaButton } from "../../components/CustomButtons/FaButton";
import { CustomModal } from "components/CustomModal/CustomModal";
import QR from "../../components/QR/Qr";
export function PersonQRButton({ personID, hash, ...rest }) {
  const [qr, setQr] = useState(hash);
  const [open, toggle] = useState(false);
  const reloadQr = async id => {
    const fetch = new AuthFetch(`api/persons/${id}`);
    let user = (await fetch.put({ hash: true })).data;
    setQr(user.hash);
  };
  return (
    <>
      <FaButton
        {...rest}
        onClick={() => {
          toggle(!open);
        }}
        icon="qrcode"
      />

      <CustomModal open={open} onClose={() => toggle(!open)}>
        <QR
          onReload={() => {
            reloadQr(personID);
          }}
          data={JSON.stringify({ h: qr })}
        />
      </CustomModal>
    </>
  );
}
