// @material-ui/icons
import Dashboard from "@material-ui/icons/Dashboard";
// core components/views
import DashboardPage from "views/Dashboard/Dashboard.jsx";
// mis views
import { Sandbox } from "views/Sandbox/Sandbox.jsx";
import { SandboxAnd } from "views/Sandbox/SandboxAnd.jsx";
import { ProfileContainer } from "../views/Users/ProfileContainer";
import { UsersView } from "../views/Users/UsersView";
import { LoginManager } from "../utils/LoginManager";
import { HousesView } from "../views/Houses/HousesView";
import { HouseView } from "../views/Houses/HouseView";
import { DoorsContainer } from "../views/Access/DoorsContainer";
import { DevicesContainer } from "./../views/Access/DevicesContainer";
import { LogsContainer } from "./../views/Log/LogsContainer";
import { FinancesContainer } from "../views/Finances/FinancesContainer";
import { AsyncHistory } from "../views/Access/AsyncHistory";
import { AddressContainer } from "../views/Houses/Address";
import { DebtsContainer } from "../views/Finances/DebtsContainer";
import { PaymentByUser } from "../views/Finances/PaymentByUser";
import { UnrecognizedPaymentsTable } from "../views/Finances/UnrecognizedPaymentsTable";
import { ServiceContainer } from "../views/Finances/ServiceContainer";
import { LegoAccess } from "../views/Access/LegoAccess";

export class DashboardRoutes {
  get = () => {
    const login = new LoginManager();
    let profile = login.getToken();

    if (!profile) {
      return [];
    }

    profile = profile.permissions;
    let profileRender = this.routes.filter(item => {
      let accessCounter = false;
      if (typeof item.permissions === "undefined") {
        return true;
      }

      for (let index in item.permissions) {
        accessCounter = login.hasPermission(item.permissions[index]);
      }
      return accessCounter;
    });

    return profileRender;
  };

  routes = [
    {
      redirect: true,
      path: "/",
      to: "/dashboard",
      navbarName: "Redirect"
    },
    {
      path: "/dashboard",
      sidebarName: "Inicio",
      navbarName: "Inicio",
      icon: Dashboard,
      component: DashboardPage
    },
    {
      parent: true,
      sidebarName: "Usuarios",
      navbarName: "Usuarios",
      icon: "users",
      permissions: ["Usuarios"]
    },
    {
      path: "/sandbox2",
      childOf: "demo",
      sidebarName: "SandboxAnd",
      navbarName: "SandboxAnd",
      icon: "user-cog",
      permissions: ["Usuarios", "Accesos"],
      component: SandboxAnd
    },
    {
      path: "/sandbox/:id?",
      childOf: "demo",
      sidebarName: "Sandbox",
      navbarName: "Sandbox",
      icon: "user-cog",
      permissions: ["Usuarios" /* , "Accesos" */],
      component: Sandbox
    },
    {
      path: "/personas/:types?",
      sidebarName: "Personas",
      childOf: "Usuarios",
      navbarName: "Personas",
      icon: "users",
      permissions: ["Usuarios", "Accesos"],
      component: UsersView
    },
    {
      path: "/personas/propietario",
      sidebarName: "Propietario",
      navbarName: "Propietario",
      childOf: "Usuarios",
      icon: "home",
      permissions: ["Usuarios", "Accesos"]
    },
    {
      path: "/personas/residente",
      sidebarName: "Residente",
      navbarName: "Residente",
      childOf: "Usuarios",
      icon: "home",
      permissions: ["Usuarios", "Accesos"]
    },
    {
      path: "/personas/proveedor",
      sidebarName: "Proveedor",
      navbarName: "Proveedor",
      childOf: "Usuarios",
      icon: "building",
      permissions: ["Usuarios", "Accesos"]
    },
    {
      path: "/personas/empleado",
      sidebarName: "Empleados",
      navbarName: "Empleados",
      childOf: "Usuarios",
      icon: "user-tie",
      permissions: ["Usuarios", "Accesos"]
    },
    {
      path: "/perfiles",
      sidebarName: "Perfiles",
      navbarName: "Perfiles",
      childOf: "Configuración",
      icon: "address-book",
      permissions: ["Usuarios"],
      component: ProfileContainer
    },
    {
      path: "/accesos/:door?",
      childOf: "Seguridad",
      sidebarName: "Accesos",
      navbarName: "Accesos",
      icon: "car",
      permissions: ["Accesos"],
      component: LegoAccess
    },
    {
      path: "/historial/accesos",
      childOf: "Seguridad",
      sidebarName: "Registro",
      navbarName: "Registro",
      icon: "clock",
      permissions: ["Accesos"],
      component: AsyncHistory
    },
    {
      path: "/casas",
      sidebarName: "Casas",
      navbarName: "Casas",
      childOf: "Terrenos",
      icon: "home",
      permissions: ["Usuarios"],
      component: HousesView
    },
    {
      path: "/domicilios",
      sidebarName: "Domicilios",
      navbarName: "Domicilios",
      childOf: "Terrenos",
      icon: "map-marked",
      permissions: ["Usuarios"],
      component: AddressContainer
    },
    {
      parent: true,
      sidebarName: "Terrenos",
      navbarName: "Terrenos",
      icon: "landmark",
      permissions: ["Usuarios"]
    },
    {
      path: "/residentes/:id",
      hidden: true,
      sidebarName: "Residentes",
      navbarName: "Residentes",
      icon: "landmark",
      permissions: ["Usuarios"],
      component: HouseView
    },
    {
      path: "/puertas",
      sidebarName: "Puertas",
      navbarName: "Puertas",
      childOf: "Configuración",
      icon: "dungeon",
      permissions: ["Usuarios"],
      component: DoorsContainer
    },
    {
      path: "/dispositivos",
      sidebarName: "Dispositivos",
      navbarName: "Dispositivos",
      childOf: "Configuración",
      icon: "microchip",
      permissions: ["Usuarios"],
      component: DevicesContainer
    },
    {
      path: "/bitacora",
      sidebarName: "Bitacora",
      navbarName: "Bitacora",
      childOf: "Configuración",
      icon: "clipboard-list",
      permissions: ["Usuarios"],
      component: LogsContainer
    },
    {
      parent: true,
      sidebarName: "Seguridad",
      navbarName: "Seguridad",
      icon: "shield-alt",
      permissions: ["Usuarios", "Accesos"]
    },
    {
      parent: true,
      sidebarName: "Configuración",
      navbarName: "Configuración",
      icon: "cog",
      permissions: ["Usuarios"]
    },
    {
      parent: true,
      sidebarName: "Finanzas",
      navbarName: "Finanzas",
      icon: "chart-line",
      permissions: ["Usuarios"]
    },
    {
      path: "/cuenta/:id?",
      childOf: "Finanzas",
      sidebarName: "Finca",
      navbarName: "Finca",
      icon: "home",
      permissions: ["Usuarios" /* , "Accesos" */],
      component: DebtsContainer
    },
    {
      path: "/finanzas",
      sidebarName: "Contaduria",
      navbarName: "Contaduria",
      childOf: "Finanzas",
      icon: "money-bill",
      permissions: ["Usuarios"],
      component: FinancesContainer
    },
    {
      path: "/cargos/persona/:id?",
      sidebarName: "Persona",
      navbarName: "Persona",
      childOf: "Finanzas",
      icon: "users",
      permissions: ["Usuarios"],
      component: PaymentByUser
    },
    {
      path: "/pagos/no_reconocidos",
      sidebarName: "No reconocidos",
      navbarName: "No reconocidos",
      childOf: "Finanzas",
      icon: "question-circle",
      permissions: ["Usuarios"],
      component: UnrecognizedPaymentsTable
    },
    {
      path: "/servicios",
      sidebarName: "Servicios",
      navbarName: "Servicios",
      childOf: "Finanzas",
      icon: "calendar",
      permissions: ["Usuarios"],
      component: ServiceContainer
    },
    {
      parent: true,
      sidebarName: "demo",
      navbarName: "demo",
      icon: "user-tie",
      permissions: ["Usuarios"]
    }
  ];
}
