import React, { Component } from "react";
import { AuthFetch } from "../../utils/AuthFetch";
import { numberToMoney } from "../../core/helpers";
import { CustomModal } from "../../components/CustomModal/CustomModal";
import { PayInput } from "./PayInput";
import "./scss/ListDebt.scss";
import { DataTable } from "../../components/Table/DataTable";
import { FancyCheckBox } from "../../components/CustomInput/FancyCheckBox";
import Button from "components/CustomButtons/Button.jsx";
import { Translator } from "../../utils/Translator";
import { ConditionalWall } from "../../components/FilterWall/ConditionalWall";
import { FaButton } from "../../components/CustomButtons/FaButton";
import { PanelContainer } from "../../components/Panel/PanelContainer";
import { HouseSelector } from "../Houses/HouseSelector";
import GridItem from "components/Grid/GridItem.jsx";
import { calcTotals } from "./core/FinancesUtils";
import { CsvButton } from "../../components/Table/CsvButton";
import { PrintFromURL } from "../../components/Prints/PrintFromURL";
import { ModalService } from "./ServicesComponents";

/* eslint eqeqeq: 0*/
export class DebtsContainer extends Component {
  state = {
    content: [],
    charge: {},
    total: 0,
    houseID: null,
    houseCode: null,
    openPayModal: false,
    charges: {},
    csvData: []
  };
  constructor(props) {
    super(props);
    this.state.houseID = props.match.params.id;
  }
  async loadContent() {
    const houseID = this.state.houseID;
    if (houseID == null) {
      this.setState({
        openPayModal: false
      });
      return false;
    }
    const fetch = new AuthFetch("api/charge");
    let data = (await fetch.get({ houseID })).data;
    let csvData = data;
    const charges = calcTotals(data);
    const total = charges.total;
    data = data.map(item => {
      return [
        item.id,
        item.paymentDate,
        item.import,
        new Translator(item.type).get(),
        item.nextRecharge || "Sin intereses",
        <>
          <ConditionalWall conditional={item.agreement != -1}>
            <FancyCheckBox
              key={item.id + "chck"}
              title={"Convenio"}
              value={item.agreement === 1}
              onChange={() => {
                if (item.agreement == -1) {
                  return false;
                }
                const fetch = new AuthFetch(`api/charge/${item.id}`);
                fetch
                  .put({
                    agreement: (item.agreement == 0) * 1
                  })
                  .then(() => {
                    this.loadContent();
                  });
              }}
            ></FancyCheckBox>
          </ConditionalWall>
          <ConditionalWall conditional={item.type != "payment"}>
            <FaButton
              icon={"trash"}
              style={{ color: "red" }}
              size={"14px"}
              title="Eliminar"
              onClick={() => {
                const fetch = new AuthFetch(`api/charge/${item.id}`);
                fetch.delete({ type: item.type }).then(() => {
                  this.loadContent();
                });
              }}
            ></FaButton>
          </ConditionalWall>
        </>
      ];
    });
    this.setState({
      content: data,
      chargeID: 0,
      openPayModal: false,
      total: total,
      charges,
      csvData
    });
  }
  async loadHouse(houseID, houseCode) {
    if (houseCode !== null || houseID === null) {
      return false;
    }
    const fetch = new AuthFetch(`api/houses/${houseID}`);
    const data = (await fetch.get()).data;
    this.setState({ houseCode: data.code });
  }
  componentDidMount() {
    this.loadHouse(this.state.houseID, this.state.houseCode);
    this.loadContent();
  }
  render() {
    const {
      content,
      openPayModal,
      total,
      houseID,
      houseCode,
      csvData
    } = this.state;
    return (
      <>
        <Button
          onClick={() => {
            const fetch = new AuthFetch(`api/charge/${houseID}/unpaid`);
            fetch.post().then(() => {
              this.loadContent();
            });
          }}
        >
          Demo: Generar Recargo
        </Button>

        <ConditionalWall conditional={!houseID}>
          <PanelContainer
            title="Terrenos"
            subtitle="Seleccione el terreno con el cual trabajar."
          >
            <GridItem xs={12}>
              <HouseSelector
                onChange={async house => {
                  this.props.history.push(`/cuenta/${house.id}`);
                  await this.setState({
                    houseID: house.id,
                    houseCode: house.code
                  });
                  this.loadContent();
                }}
              />
            </GridItem>
          </PanelContainer>
        </ConditionalWall>
        <ConditionalWall conditional={houseID}>
          <DataTable
            title={"Lista de Cargos de " + houseCode}
            subtitle={
              total > 0
                ? `Deuda total: ${numberToMoney(total)}`
                : `Saldo a favor ${numberToMoney(total)}`
            }
            titles={[
              "ID",
              "Fecha",
              "Tarifa",
              "Tipo",
              "Pronto pago",
              "Acciones"
            ]}
            prev={
              <>
                <PrintFromURL
                  title="Imprimir nota de remision"
                  url={`/templates/charges/${houseID}`}
                />
                <CsvButton
                  fileName={"Lista de Cargos de " + houseCode}
                  content={csvData.map(item => [
                    item.id,
                    item.paymentDate,
                    item.import,
                    new Translator(item.type).get()
                  ])}
                  titles={["ID", "Fecha", "Tarifa", "Tipo"]}
                />

                <ModalService
                  houseID={houseID}
                  onSave={() => {
                    this.loadContent();
                  }}
                />
              </>
            }
            content={content}
          >
            <Button
              fullWidth
              variant="contained"
              color="primary"
              onClick={() => this.setState({ openPayModal: true })}
            >
              <h4>
                {total > 0
                  ? `El terreno tiene un adeudo de: ${numberToMoney(total)}`
                  : "Abonar"}
              </h4>
            </Button>
          </DataTable>
        </ConditionalWall>

        <CustomModal open={openPayModal}>
          <PayInput
            id={houseID}
            total={total}
            onChange={() => {
              this.loadContent();
            }}
          ></PayInput>
        </CustomModal>
      </>
    );
  }
}
