import React, { Component } from "react";
import { DataTable } from "../../components/Table/DataTable";
import { AuthFetch } from "../../utils/AuthFetch";
import { DoorSelector } from "./DoorSelector";
import "./scss/Devices.scss";
import { IconButton } from "@material-ui/core";
import { FontIcon } from "../../components/Icons/FontIcon";
import swal from "sweetalert";
export class DevicesContainer extends Component {
  state = { devices: [] };
  componentDidMount() {
    this.loadDevices();
  }
  async testDevice(id) {
    const fetch = new AuthFetch(`api/doors/${id}/run`);
    let content = (await fetch.get()).data;
    if (content === true) {
      swal("Excelente", "Comando Ejecutado", "success");
    }
  }
  async loadDevices() {
    const fetch = new AuthFetch("api/devices");
    let devices = (await fetch.get()).data;
    this.setState({
      devices:
        devices.map((device, key) => {
          return [
            device.macAddress,
            <DoorSelector
              className="doorSelector"
              value={parseInt(device.comandID)}
              onChange={content => {
                this.saveComand(device.id, content.value);
              }}
            />,
            <React.Fragment>
              <IconButton
                size="small"
                color="primary"
                onClick={() => {
                  this.testDevice(device.comandID);
                }}
              >
                <FontIcon iconName="fa-play" style={{ fontSize: "13px" }} />
              </IconButton>
              <IconButton
                size="small"
                color="secondary"
                onClick={() => {
                  this.deleteDevice(device.id);
                }}
              >
                <FontIcon iconName="fa-trash" style={{ fontSize: "13px" }} />
              </IconButton>
            </React.Fragment>
          ];
        }) || []
    });
  }
  saveComand(id, comandID) {
    const fetch = new AuthFetch(`api/devices/${id}`);
    fetch.put({ comandID });
  }
  async deleteDevice(id) {
    const fetch = new AuthFetch(`api/devices/${id}`);
    await fetch.delete();
    this.loadDevices();
  }
  render = () => {
    const { devices } = this.state;
    return (
      <DataTable
        title="Dispositivos sincronizados"
        subtitle="dispositivos esperando sincronizacion"
        titles={["Dirección MAC", "Comando", "Acciones"]}
        content={devices}
        className="devices"
      />
    );
  };
}
