import React, { Component } from "react";
import { PersonSelector } from "../Users/PersonSelector";
import { HousesByPerson } from "../Houses/HousesByPerson";
import { numberToMoney } from "../../core/helpers";
import { calcTotals, setBalance } from "./core/FinancesUtils";
import { UnrecognizedPaymentSelector } from "./ UnrecognizedPayments";
import { ConditionalWall } from "../../components/FilterWall/ConditionalWall";
import { FancyCheckBox } from "../../components/CustomInput/FancyCheckBox";
import { PanelContainer } from "./../../components/Panel/PanelContainer";
import GridItem from "components/Grid/GridItem.jsx";
export class PaymentByUser extends Component {
  state = {
    debt: 0,
    balance: 0,
    payments: 0,
    unrecognized: null,
    useUnrecognized: false
  };
  constructor(props) {
    super(props);
    this.state.userID = props.match.params.id || false;
  }

  render() {
    const {
      debt,
      balance,
      payments,
      unrecognized,
      userID,
      useUnrecognized
    } = this.state;
    let money = balance - payments;
    let debtType = debt > 0 ? "una deuda" : "un saldo a favor";
    return (
      <PanelContainer subtitle=" " title="Cargos por Persona">
        <GridItem xs={12}>
          <ConditionalWall
            conditional={userID}
            or={
              <PersonSelector
                type={"house"}
                onChange={ev => {
                  this.props.history.push(`${ev.value}`);
                  this.setState({ userID: ev.value });
                }}
              />
            }
          >
            <>
              <h2>
                {`Tiene ${debtType} por un total de :`} {numberToMoney(debt)}
              </h2>

              <FancyCheckBox
                title="Pagos no reconocidos"
                onChange={() => {
                  this.setState({ useUnrecognized: !useUnrecognized });
                }}
              >
                Usar pagos no reconocidos
              </FancyCheckBox>
              <ConditionalWall conditional={useUnrecognized}>
                <UnrecognizedPaymentSelector
                  onChange={ev => {
                    const balance = ev.amount - (ev.balance || 0);
                    this.setState({ balance, unrecognized: ev });
                  }}
                />
              </ConditionalWall>

              <h4>
                {money >= 0 ? "Saldo disponible:" : "Limite superado por: "}{" "}
                {numberToMoney(money)}
              </h4>
              <HousesByPerson
                id={userID}
                onDataLoad={data => {
                  let total = 0;
                  for (let item of data) {
                    total += calcTotals(item.charges).total;
                  }
                  this.setState({ debt: total });
                }}
                onPaymentChange={payments => {
                  if (unrecognized == null) {
                    return false;
                  }
                  let total = setBalance(unrecognized.id, {
                    amount: unrecognized.amount,
                    balance: unrecognized.balance,
                    payments
                  });
                  this.setState({ balance: total });
                }}
              />
            </>
          </ConditionalWall>
        </GridItem>
      </PanelContainer>
    );
  }
}
