import { optionalFn } from "../../../core/helpers";

export class AccessTimer {
  cancelInterval = false;
  timeOutCallback = {};
  eachCallback = {};
  interval = {};
  time = 1;
  constructor(time) {
    this.time = time;
  }
  start() {
    this.onAccessTimeout(this.time);
    return this;
  }
  each(callback) {
    this.eachCallback = callback;
    return this;
  }
  cancel(callback) {
    this.cancelInterval = true;
    this.cancelCallback = callback;
    return this;
  }
  onTimesOut(callback) {
    this.timeOutCallback = callback;
    return this;
  }
  run() {
    this.time = 0;
    return this;
  }
  onAccessTimeout(time = 1) {
    this.interval = setInterval(() => {
      this.time -= 1;
      this.eachCallback(this.time);
      console.log(this.cancelInterval, new Date(), this.interval);
      if (this.cancelInterval) {
        this.cancelInterval = false;
        clearInterval(this.interval);
        optionalFn(this.cancelCallback)(time);
      }
      if (this.time === 0) {
        optionalFn(this.timeOutCallback)(this.time);
        clearInterval(this.interval);
      }
    }, 1000);
  }
}
