import React from "react";
import { AuthFetch } from "../../utils/AuthFetch";
import { CustomSwitchAccess } from "../../components/CustomSwitchAccess/CustomSwitchAccess";
import { AnchorList } from "../../components/Anchors/AnchorList";
import { CustomModal } from "components/CustomModal/CustomModal";
import QR from "../../components/QR/Qr";
import { Image } from "../../components/Images/Image";
import { FaButton } from "../../components/CustomButtons/FaButton";
import { AsyncDataTable } from "../../components/Table/AsycnDataTable";
import { PersonsContainer } from "../Forms/PersonsContainer";
import { UserKeysButton } from "./UserKeys";
/**
 * @description componente de pruebas
 */

export class UsersTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: [],
      id: null,
      type: props.type,
      openQR: false,
      openUser: false
    };
  }
  componentWillReceiveProps(nextProps) {
    const content = {};
    if (nextProps.type !== this.state.type) {
      content.type = nextProps.type;
    }
    this.setState(content);
  }
  /**
   * @description en base al tipo carga de manera asincrona la tabla de usuarios
   * @param String type
   * @return Promise
   */

  handleClose = () => {
    this.setState({ openUser: false, openQR: false, id: null });
  };
  _formatPersons = (item, key) => {
    const date = new Date(item.limitedAccess);
    const validity =
      date - 0 !== 0
        ? date.toLocaleString("es-MX", {
            year: "numeric",
            month: "long",
            day: "numeric"
          })
        : "Vitalicia";
    return [
      <Image
        src={item.photo}
        style={{
          width: "50px",
          height: "50px",
          borderRadius: "100%",
          boxShadow: "1px 1px 3px #007777"
        }}
      />,
      item.fullname,
      [<AnchorList key={`mails-${key}`} type="mailto" items={item.mails} />],
      [<AnchorList key={`phones-${key}`} type="tel" items={item.phones} />],
      item.type,
      validity,
      [
        <FaButton
          key={item.id + "Fa"}
          size="12px"
          onClick={() => {
            this.setState({ id: item.id, openUser: true });
          }}
          icon="edit"
        />,
        <FaButton
          key={item.id + 1 + "Fa"}
          size="12px"
          onClick={() => {
            this.setState({
              QRdata: {
                h: item.hash
              },
              openQR: true,
              id: item.id
            });
          }}
          icon="qrcode"
        />,

        <UserKeysButton key={`usb-${key}`} id={item.id} />,
        <CustomSwitchAccess
          key={`access-${key}`}
          id={item.id}
          hasAccess={item.hasAccess}
          expirationDate={item.limitedAccess}
          onChange={this.addUser}
        />
      ]
    ];
  };
  addUser() {}
  async reloadQr(id) {
    const fetch = new AuthFetch(`api/persons/${id}`);
    let user = await fetch.put({ hash: true });
    this.setState({
      QRdata: {
        h: user.data.hash
      }
    });
  }
  render() {
    const { id, type, openUser, openQR } = this.state;
    return (
      <main key={type}>
        <AsyncDataTable
          path="api/persons/paginated/8"
          ref="table"
          args={type ? { type } : {}}
          title={type || "Usuarios"}
          subtitle={`Control basico de ${type || "Usuarios"}.`}
          titles={{
            photo: "Foto",
            fullname: "Nombre",
            mails: "Correo",
            phones: "Telefono",
            type: "Tipo",
            validity: "Vigencia",
            actions: "Acciones"
          }}
          formatContent={this._formatPersons}
          modalOpen={openUser}
          onClose={this.handleClose}
          inModal={
            <PersonsContainer
              submission={this.handleClose}
              type={type}
              edit={id}
            />
          }
        />
        <CustomModal open={openQR} onClose={this.handleClose}>
          <QR
            onReload={() => {
              this.reloadQr(id);
            }}
            data={JSON.stringify(this.state.QRdata)}
          />
        </CustomModal>
      </main>
    );
  }
}
