import React from "react";
import GridContainer from "components/Grid/GridContainer.jsx";
import GridItem from "components/Grid/GridItem.jsx";
import { ReduceUserCard } from "../Users/ReduceUserCard";
import { AuthFetch } from "../../utils/AuthFetch";
import { ResidentsContainer } from "../Users/ResidentsContainer";
import { PanelContainer } from "../../components/Panel/PanelContainer";
/**
 * @description componente de pruebas
 */
/* eslint eqeqeq: 0*/

export class HouseView extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      code: props.match.params.id || "",
      ownerID: null,
      residentID: null,
      openResidentModal: false,
      openGuestModal: false
    };
  }
  componentDidMount() {
    let code = {};
    let owner = {};
    if (this.props.match) {
      code = { code: this.props.match.params.id };
    }
    if (this.props.ownerID) {
      owner = { ownerID: this.props.ownerID };
    }
    this.loadData({
      ...code,
      ...owner
    });
  }
  async loadData(by) {
    if (!by) {
      return false;
    }

    const fetch = new AuthFetch("api/houses/");
    const houses = (await fetch.get(by)).data[0];
    const { ownerID, residentID, code } = houses;
    this.setState({
      code,
      ownerID,
      residentID
    });
  }
  render = () => {
    const {
      code,
      ownerID,
      residentID,
      openResidentModal,
      openGuestModal
    } = this.state;
    const equalID = ownerID === residentID;
    return (
      <GridContainer alignItems="stretch">
        <GridItem xs={12}>
          <h1>Terreno:{code}</h1>
          <hr />
        </GridItem>
        <GridItem md={equalID ? 12 : 6} xs={12}>
          <PanelContainer title="Titular" subtitle=" ">
            <ReduceUserCard id={ownerID} />
          </PanelContainer>
        </GridItem>
        {equalID ? null : (
          <GridItem md={6} xs={12}>
            <PanelContainer title="Residente" subtitle=" ">
              <ReduceUserCard id={residentID} />
            </PanelContainer>
          </GridItem>
        )}
        <GridItem xs={12} lg={6}>
          <PanelContainer
            onAdd={() => {
              this.setState({
                openResidentModal: true,
                openGuestModal: false
              });
            }}
            title="Habitantes"
            subtitle="Residentes de esta vivienda"
          >
            <ResidentsContainer
              code={code}
              modal={openResidentModal}
              type="Residente"
              onClose={() => {
                this.setState({
                  openResidentModal: false,
                  openGuestModal: false
                });
              }}
            />
          </PanelContainer>
        </GridItem>
        <GridItem xs={12} lg={6}>
          <PanelContainer
            title="Invitados"
            subtitle=" "
            onAdd={() => {
              this.setState({
                openResidentModal: false,
                openGuestModal: true
              });
            }}
          >
            <ResidentsContainer
              code={code}
              modal={openGuestModal}
              type="Invitado"
              onClose={() => {
                this.setState({
                  openResidentModal: false,
                  openGuestModal: false
                });
              }}
            />
          </PanelContainer>
        </GridItem>
      </GridContainer>
    );
  };
}
